import {Injectable} from '@angular/core';
import {HttpService} from "./http.service";
import {RuleModel} from "../shared-components/filter/filter.component";
import {Subject} from "rxjs/Subject";
import List from "identical-list";
import {UtilService} from "./util.service";
import {AuthenticateService} from "./authenticate.service";
import {Collection} from "./models/collection";

@Injectable()
export class CollectionService {

  onCollectionListUpdated = new Subject();

  collectionList = new List<Collection>();
  collectionHashById = {};
  collectionHashByName = {};

  constructor(private auth: AuthenticateService,
              private http: HttpService,
              private util: UtilService) {
    this.init();
  }

  getCollectionHash() {
    return this.collectionHashById;
  }

  forceUpdate() {
    this._loadCollections(10000);
  }

  listCollection(start?: number, end?: number, filters?: Array<RuleModel>) {
    let subset = this.collectionList.asArray();
    if (filters instanceof Array) {
      for (let filter of filters) {
        // subset = subset.filter(p => {
        //   switch (filter.name_rule) {
        //     case 'name': {
        //       return this.util.searchCompare(p.product_name, filter.value, filter.compair_rule);
        //     }
        //   }
        // });
      }
    }
    return subset.slice(start || 0, end || subset.length);
  }

  async loadCollection(query?: any): Promise<Array<Collection>> {
    return this.http.post(`api/shop.Collection/GetCollections`, query || {}).toPromise()
      .then(res => res.collections.map(c => new Collection(c)));
  }

  async createCollection(data: Collection): Promise<Collection> {
    let res = await this.http.post("api/shop.Collection/CreateCollection", data).toPromise();
    this.collectionList.add(new Collection(res));
    this.collectionHashById[res.id] = res;
    this.collectionHashByName[res.name] = res;
    this._invokeUpdate();
    return res;
  }

  async updateColletion(data: Collection): Promise<Collection> {
    let res = await this.http.post("api/shop.Collection/UpdateCollection", data).toPromise();
    this.collectionList.add(data);
    this.collectionHashById[res.id] = res;
    this.collectionHashByName[res.name] = res;
    this._invokeUpdate();
    return res;
  }

  async deleteCollection(id: string) {
    let res = await this.http.post("api/shop.Collection/DeleteCollection", {id}).toPromise();
    this.collectionList.deleteId(id);
    this.collectionHashByName[this.collectionHashById[id].name] = null;
    this.collectionHashById[id] = null;
    this._invokeUpdate();
    return res;
  }

  async addProductCollection(collection_id: string, product_ids: Array<string>) {
    return this.http.post("api/shop.Collection/UpdateProductsCollection", {
      collection_id: collection_id, product_ids
    }).toPromise();
  }

  async removeProductsCollection(collection_id: string, product_ids: Array<string>) {
    return this.http.post("api/shop.Collection/RemoveProductsCollection", {
      collection_id: collection_id, product_ids
    }).toPromise();
  }

  private async init() {
    this._loadCollections(1000);
    // this.util.createFetchLoop(this._loadCollections.bind(this, 10000));
    this.auth
    .authenInfo
    .distinctUntilKeyChanged('token')
    .subscribe(info => {
      this._loadCollections(10000, true);
    });
  }

  private _invokeUpdate() {
    this.onCollectionListUpdated.next({
      collectionList: this.collectionList,
      collectionHashById: this.collectionHashById,
      collectionHashByName: this.collectionHashByName
    });
  }

  private async _loadCollections(limit, replace = false) {
    try {
      let collections = await this.loadCollection({paging: {limit: limit}});
      if (replace) {
        this.collectionList = new List<Collection>(collections);
      } else {
        this.collectionList.concat(collections);
      }
      collections.forEach(c => {
        this.collectionHashById[c.id] = c;
        this.collectionHashByName[c.name] = c;
      });
      this._invokeUpdate();
      // }
    } catch (e) {
      debug.error((e));
    }
  }
}



