import { Injectable } from '@angular/core';
import { OrderService } from './order.service';
import { ShopService } from './shop.service';
import { UserService } from './user.service';
import { AuthenticateService } from './authenticate.service';
declare const fbq: any;

@Injectable()
export class FacebookPixelService {

  constructor(private orderService: OrderService,
    private shopService: ShopService,
    private userService: UserService,
    private auth: AuthenticateService) { }

  sendPageView() {
    fbq('track', 'PageView');
  }

  async sendUserProperties() {
    try {
      if (!this.auth) {
        return;
      }

      let orderCount = [];
      if (this.auth.token) {
        let res = await this.orderService.getShopOrders(this.auth.token, { paging: { limit: 1 } });
        orderCount = res.paging.total || 0;
      }

      let products = await this.shopService.loadProduct();

      let resProviders = await this.userService.getProvider();
      let providers = resProviders.providers || [];

      let shop = this.auth.shop;
      let user = this.auth.user;

      fbq('init', '264334294378919', { uid: this.auth.user.id });

      fbq('setUserProperties', '264334294378919',
        {
          $city: shop && shop.address && shop.address.province || '',
          $country: 'Viet Nam',
          $currency: 'VND',
          $state: '',
          $user_type: this.mapAccountType(this.auth.accounts),
          nShop: this.numberOfShop(this.auth.accounts),
          nProduct: products.length,
          nOrder: orderCount,
          haravan: providers.length ? providers[0].name : '',
          shopName: shop && shop.name || '',
          shopEmail: shop && shop.email || '',
          shopPhone: shop && shop.phone || '',
          accountName: user && user.full_name || '',
          accountEmail: user && user.email || '',
          accountPhone: user && user.phone || ''
        }
      )
    } catch (e) { }
  }

  numberOfShop(accounts) {
    return accounts.reduce((x, y) => {
      if (y.type == 'shop') {
        return x + 1;
      }

      return x;
    }, 0);
  }

  mapAccountType(accounts) {
    let hasSupplierAcc = accounts.reduce((x, y) => x || y.type == 'supplier', false);

    let hasShopAcc = accounts.reduce((x, y) => x || y.type == 'shop', false);

    if (hasShopAcc && hasSupplierAcc) {
      return 'SHOP-SUPPLIER';
    }

    if (hasShopAcc) {
      return 'SHOP';
    }

    if (hasSupplierAcc) {
      return 'SUPPLIER';
    }

    return '';
  }
}



