import {Injectable} from '@angular/core';
import {HttpService} from "./http.service";
import List from "identical-list";
import {AuthenticateService} from "./authenticate.service";
import {UtilService} from "./util.service";
import {Subject} from "rxjs/Subject";
import {SyncService} from "./sync.service";
import {Product, ShopProduct, Variant} from "./models/product";
import {ProductSrc, ProductSrcVariant} from "./models/product-source";
import {ProductSrcCategory} from "./models/product-source-categories";

import {Brand} from "./models/brand";

@Injectable()
export class ShopService {

  DEFAULT_QUANTITY = 1000;

  onProductSrcUpdate = new Subject();
  onProductSrcCategoriesUpdated = new Subject<List<ProductSrcCategory>>();

  productSrcList = new List<ProductSrc>();
  productSrcCategories = new List<ProductSrcCategory>();

  onProductIDListUpdated = new Subject<List<any>>();
  onProductListUpdated = new Subject<List<ShopProduct>>();
  onShopServiceReady = new Subject();
  productIDList = new List<any>();
  productList = new List<ShopProduct>();
  total = 0;
  brandsList = new List<Brand>();

  private _isLoading = true;
  loadingCategories = true;
  onLoadingCategoriesChange = new Subject<any>();
  onLoadingProductChange = new Subject<boolean>();

  get isLoading() {
    return this._isLoading;
  }

  set isLoading(loading) {
    if (loading != this._isLoading && loading == false) {
      this.onShopServiceReady.next();
    }
    this._isLoading = loading;
  }

  constructor(private http: HttpService,
              private auth: AuthenticateService,
              private haravanService: SyncService,
              private util: UtilService) {
    this.init();
    this.auth
    .authenInfo
    .distinctUntilKeyChanged('token')
    .subscribe(info => {
      if (info.token) {
        this._loadProduct(100);
        this._loadCategories();
      }
    });
    this.haravanService.onLogUpdated.subscribe(({}) => {
      this.productList.forEach(p => {
        p.status_display = this._statusMap(p);
      });
    });
    window['__shopService'] = this;
    this.fetchBrands();
  }

  async init() {
    this.isLoading = true;
    this._loadCategories();
    let limit = 10000;
    await this._loadProduct(limit);
    // let refreshProduct = async () => {
    //   this.timerService.setTimeout(refreshProduct, 30000, 'GET_PRODUCT', true);
    // };
    // refreshProduct();
    let res = await this.getShopProductSources();
    this.productSrcList = new List(res.product_sources);
  }

  async checkEmptyProducts() {
    const products = await this.loadProduct({paging: {limit: 1}});
    return products.length > 0;
  }

  getLoadStatus() {
    return this.isLoading;
  }

  private _invokeUpdate() {
    this.onProductListUpdated.next(this.productList);
    this.onProductIDListUpdated.next(this.productIDList);
  }

  private _loadCategories() {
    this.getProductSourceCategories('custom').then((res) => {
      if (!res.categories) {
        return;
      }
      this.productSrcCategories = new List(res.categories);
      this.loadingCategories = false;
      this.onLoadingCategoriesChange.next(this.loadingCategories);
      this.onProductSrcCategoriesUpdated.next(new List(res.categories));
    });

    this.onProductSrcCategoriesUpdated.subscribe((cats) => {
      this.productSrcCategories = cats;
    });
  }

  private async _loadProduct(limit) {

    try {
      let products: any = await this.loadProduct({paging: {limit: limit}});
      this.productList = new List(products);
      this.productIDList = new List(products.map(p => p.id));
      this._invokeUpdate();
      // }
    } catch (e) {

    }
    this.isLoading = false;
    this.onLoadingProductChange.next(this.isLoading);
  }

  fetchBrands() {
    return this.http.post("api/shop.Brand/GetBrands", {}).toPromise()
      .then(res => {
        let brands = res['brands'];
        brands = brands.sort((a, b) => (a.name < b.name) ? -1 : 1);
        this.brandsList = new List(brands);
        return brands;
      })
      .catch((e) => {
        debug.error("[ShopService:fetchBrands]", e);
        return [];
      });
  }

  pullProducts() {
    this.updateProductSrcCategories();
    this._loadProduct(10000);
  }

  getBrand(id) {
    let brand = this.brandsList.get(id);
    return brand || null;
  }

  mapVariantImages(variant: Variant, product?: any, isReverse = false) {
    let image_url = variant.image_urls.length && variant.image_urls[0] || '/assets/images/placeholder_2.png';
    if (isReverse) {
      image_url = `${(product.image_urls && product.image_urls[0] ? product.image_urls[0] : product.info && product.info.image_urls[0]) || "assets/images/placeholder_2.png"}`;
    }

    return Object.assign(variant, {
      image_urls: variant.image_urls,
      image_url: `url(${image_url})`
    });
  }

  private _productMap(p): ShopProduct {
    let v = p.variants[0];
    let product = new ShopProduct({
      id: p.id,
      name: p.name,
      description: p.description,
      short_desc: p.short_desc,
      desc_html: p.desc_html,
      tags: p.tags,
      price: v.retail_price,
      wholesale_price: v.info.wholesale_price,
      cost_price: v.info.cost_price,
      list_price_min: v.info.retail_price_min,
      list_price_max: v.info.retail_price_max,
      quantity_available: p.info.quantity_available,
      image: `url(${(p.image_urls && p.image_urls[0] ? p.image_urls[0] : p.info.image_urls[0]) || "assets/images/placeholder_2.png"})`,
      image_urls: p.image_urls,
      status: this._statusConvert(p.status),
      haravan_id: p.haravan_id,
      brand_id: p.brand_id,
      collection_ids: p.collection_ids,
      category_id: p.info.category_id != '0' ? p.info.category_id : p.info.product_source_category_id,
      brand_info: this.getBrand(p.info.s_brand_id),
      variants: p.variants.map(variant => this._variantMap(p.name, variant, {image: p.image_urls[0]})),
      o_data: p,
      attributes: (p.info.attributes && p.info.attributes.length > 0) ? p.info.attributes : p.info.x_attributes,
      product_source_id: p.product_source_id,
      product_source_name: p.product_source_name,
      product_source_type: p.product_source_type,
      code: p.info.code
    });

    let oldProduct = this.productList.get(p.id);
    if (oldProduct) {
      product.p_data = oldProduct.p_data;
    }
    product.status_display = this._statusMap(product);

    return product;
  }

  private _variantMap(product_name, v, variantDefault = {image: "assets/images/placeholder_2.png"}): Variant {
    return new Variant({
      id: v.id,
      name: product_name,
      list_price: v.info.list_price,
      retail_price: v.retail_price,
      cost_price: v.info.cost_price,
      wholesale_price: v.info.wholesale_price,
      list_price_min: v.info.retail_price_min,
      list_price_max: v.info.retail_price_max,
      quantity_available: v.info.quantity_available,
      status: v.status,
      image_urls: v.image_urls,
      image_url: `url(${v.image_urls[0] || variantDefault.image || "assets/images/placeholder_2.png"})`,
      o_data: v,
      desc_html: v.desc_html,
      short_desc: v.short_desc,
      attributes: (v.info.attributes && v.info.attributes.length > 0) ? v.info.attributes : v.info.x_attributes,
      sku: v.info.sku
    });
  }

  private _statusConvert(status) {
    let map = {
      "Z": "default",
      "P": "active",
      "N": "disabled",
      "DEFAULT": "Z",
      "ACTIVE": "P",
      "DISABLED": "N"
    };
    return map[status.toUpperCase()] || "unknown";
  }

  private _statusMap(p: Product) {
    try {
      let {queueHash, errorLogHash} = this.haravanService;
      if (queueHash[p.id]) {
        return 'pending';
      }
      if (errorLogHash[p.id]) {
        p.p_data.haravan_error = errorLogHash[p.id];
        return 'error';
      }

      p.p_data.haravan_error = null;

      if (p.status_display == "pending") {
        return "pending";
      }

      // if (p.status == "active" && !p.haravan_id) {
      //   return "pending";
      // }
      if ((p.status == "active" || p.status == "default") && p.haravan_id) {
        return "published";
      }
    } catch (e) {
      debug.error(e);
    }


    return "new";
  }

  productCount(sub: (product: any) => boolean) {
    return this.productList.filter(sub).length;
  }

  listProduct(start?: number, end?: number, filters?: Array<any>) {
    let subset = this.productList.asArray();
    if (filters instanceof Array) {
      for (let filter of filters) {
        subset = subset.filter(p => {
          switch (filter.name_rule) {
            case 'product_name': {
              return this.util.searchCompare(p.name, filter.value, filter.compair_rule);
            }
            case 'wholesale_price': {
              return eval('p.' + filter.name_rule + filter.compair_rule + filter.value);
            }
            case 'price': {
              return eval('p.' + filter.name_rule + filter.compair_rule + filter.value);
            }
            case 'status': {
              return this.util.searchCompare(p.status, filter.value, '==');
            }
            case 'collection': {
              return p.collection_ids.indexOf(filter.value) > -1;
              // return eval('p.collection_id' + filter.compair_rule + filter.value);
            }
            case 'inventory': {
              return eval('p.quantity_available' + filter.compair_rule + filter.value);
            }
            case 'haravan_id': {
              return eval('p.haravan_id' + filter.compair_rule + filter.value);
            }
            case 'product_source_type': {
              if (filter.value == 'custom') {
                return p.product_source_type == 'custom';
              }

              return p.product_source_type != 'custom';
            }
            default:
              return this.util.searchCompare(p[filter.name_rule], filter.value, filter.compair_rule);
          }
        });
      }
    }
    return subset.slice(start || 0, end || subset.length);
  }

  loadProduct(query?: any, token?): Promise<Array<ShopProduct>> {
    let headers = this.http.createHeader(token);
    let options = this.http.createDefaultOption(headers);
    return this.http.postWithOptions(`api/shop.Product/GetProducts`, query || {}, options).toPromise()
      .then(res => {
        res.products.sort(function (a, b) {
          let x: any = new Date(b.info.updated_at);
          let y: any = new Date(a.info.updated_at);
          return x - y;
        });
        this.total = res.paging.total;
        return res.products.map(this._productMap.bind(this));
      }).catch((e) => {
        debug.error("[ShopService:loadProduct]", e);
        return [];
      });
  }

  async addProduct(ids: Array<string>) {
    let res = await this.http.post(`api/shop.Product/AddProducts`, {ids}).toPromise();
    this.productIDList.concat(ids.map(id => {
      return {
        id
      };
    }));
    this._invokeUpdate();
    this._loadProduct(10000);
    return res;
  }

  updateProduct(data): Promise<ShopProduct> {
    return this.http.post(`api/shop.Product/UpdateProduct`, data).toPromise()
      .then(res => {
        return this._productMap(res);
      });
  }

  updateVariant(variant): Promise<Variant> {
    return this.http.post(`api/shop.Product/UpdateVariant`, variant).toPromise()
      .then(res => this._variantMap(variant.name, res));
  }

  updateVariants(variants: Array<any>): Promise<Array<Variant>> {
    return this.http.post(`api/shop.Product/UpdateVariants`, {
      updates: variants
    }).toPromise()
      .then(res => res.variants.map(this._variantMap));
  }

  updateVariantImages(data, product_name): Promise<Variant> {
    return this.http.post(`api/shop.Product/UpdateVariantImages`, data).toPromise()
      .then(res => this._variantMap(product_name, res));
  }

  updateProductTags(ids: Array<string>, tags: Array<string>) {
    let data: any = {
      ids
    };
    if (!tags.length) {
      data.delete_all = true;
    } else {
      data.replace_all = tags;
    }
    return this.http.post(`api/shop.Product/UpdateProductsTags`, data).toPromise();
  }

  updateVariantsStatus(ids: Array<string>, status: "active" | "disabled") {
    return this.http.post(`api/shop.Product/UpdateVariantsStatus`, {
      ids, status: this._statusConvert(status)
    }).toPromise();
  }

  removeIds(ids) {
    ids.forEach(id => this.productList.deleteId(id));
  }

  getShopProductSources(): Promise<any> {
    return this.http.post("api/shop.ProductSource/GetShopProductSources", {}).toPromise().catch((e) => {
      debug.error("[ShopService:getShopProductSources]", e);
      return {};
    });
  }

  addNewProductSource(shopName?: string, token?: string) {
    const productSrc = new ProductSrc({ type: 'custom', name: shopName || this.auth.shop.name });
    return this.createProductSource(productSrc, token);
  }

  createProductSource(data: ProductSrc, token?: string): Promise<ProductSrc> {
    if (!data.name) {
      throw new Error("Product source name can't be null!");
    }
    if (token) {
      const options = this.http.createCustomDefaultOption(token);
      return this.http.postWithOptions("api/shop.ProductSource/CreateProductSource", data, options).toPromise();
    }
    return this.http.post("api/shop.ProductSource/CreateProductSource", data).toPromise();
  }

  removeProductSource(): Promise<any> {
    return this.http.post("api/shop.ProductSource/RemoveProductSource", {}).toPromise();
  }

  connectProductSource(data: ProductSrc): Promise<any> {
    return this.http.post("api/shop.ProductSource/ConnectProductSource", { product_source_id: data.id}).toPromise();
  }

  createVariant(data: ProductSrcVariant): Promise<any> {
    return this.http.post("api/shop.ProductSource/CreateVariant", data).toPromise();
  }

  removeVariants(ids) {
    return this.http.post("api/shop.Product/RemoveVariants", { ids }).toPromise();
  }

  updateProductSrc() {
    this.getShopProductSources().then(res => {
      this.productSrcList = new List(res.product_sources);
      this.onProductSrcUpdate.next(this.productSrcList);
    });
  }

  updateProductSrcCategories() {
    this.getProductSourceCategories('custom').then(res => {
      this.onProductSrcCategoriesUpdated.next(new List(res.categories));
    });
  }

  createProductSrcCategory(name, parent_id, product_source_id, product_source_type): Promise<any> {
    return this.http.post("api/shop.ProductSource/CreateProductSourceCategory", {
      name: name,
      parent_id: parent_id,
      product_source_id: product_source_id,
      product_source_type: product_source_type
    }).toPromise().then(res => {
      this.productSrcCategories.add(new ProductSrcCategory(res));
      this.onProductSrcCategoriesUpdated.next(this.productSrcCategories);
      return res;
    });
  }

  updateupdateProductPSCategory(category_id, product_ids) {
    return this.http.post("api/shop.ProductSource/UpdateProductsPSCategory", {
      category_id: category_id,
      product_ids: product_ids
    }).toPromise().then(res => {
      return res;
    });
  }

  removeProductSourceCategory(id) {
    return this.http.post("api/shop.ProductSource/RemoveProductSourceCategory", {id}).toPromise();
  }

  updateProductSourceCategory(data) {
    return this.http.post("api/shop.ProductSource/UpdateProductSourceCategory", data).toPromise();
  }

  updateProductImages(body) {
    return this.http.post(`api/shop.Product/UpdateProductImages`, body).toPromise().then(res => {
      return this._productMap(res);
    });
  }

  getProductSourceCategories(type) {
    return this.http.post(`api/shop.ProductSource/GetProductSourceCategories`, { type })
      .toPromise()
      .catch((e) => {
        debug.error("[ShopService:loadFulfillments]", e);
        return {};
      });
  }

  getProduct(id) {
    return this.http.post(`api/shop.Product/GetProduct`, {id}).toPromise();
  }

  getProductsByIds(ids) {
    return this.http.post(`api/shop.Product/GetProductsByIDs`, { ids }).toPromise();
  }

  newProductCreated(id) {
    return this.getProductsByIds([id]).then((res) => {
      if (!res || !res.products || !res.products.length) {
        return;
      }
      let newProduct = this._productMap(res.products[0]);
      this.productList.asArray().unshift(newProduct);
      this.productIDList.asArray().unshift(newProduct.id);
      this._invokeUpdate();

      return newProduct;
    });
  }

  productMap(p): ShopProduct {
    let product = this._productMap(p);
    this.productList.concat([product]);
    this._invokeUpdate();
    return product;
  }

  onRemovedCategory(id) {
    this.productList.forEach(product => {
      if (product.category_id == id) {
        product.category_id = '';
      }
    })

    this._invokeUpdate();
  }

  onRemovedCollection(id) {
    this.productList.forEach(product => {
      product.collection_ids = product.collection_ids.filter(colId => colId !== id);
    })

    this._invokeUpdate();
  }

  calcShopBalance() {
    return this.http.post(`/api/shop.Summary/CalcBalanceShop`, {}).toPromise().then(res => {
      return res.balance;
    });;
  }
}



