import {Component, OnInit, ViewChild, OnDestroy} from '@angular/core';
import {UserService} from "../../../shared-services/user.service";
import {Router} from "@angular/router";
import {AuthenticateService} from '../../../shared-services/authenticate.service';
import * as validatecontraints from '../../../../libs/validation-contraints';
import { GoogleAnalyticsService } from '../../../shared-services/google-analytics.service';
import { LayoutService } from '../../services/layout.service';

declare const toastr: any;

@Component({
  selector: 'mobile-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit, OnDestroy {
  error = false;
  errorMessage = "";

  email: string;
  password: string;

  loading = false;

  resetPasswordLoading = false;
  formForgot: any = {};

  constructor(private userService: UserService,
              private router: Router,
              private auth: AuthenticateService,
              private layoutService: LayoutService,
              private gaService: GoogleAnalyticsService) {
  }

  ngOnInit() {
    this.layoutService.setDisplayFooter(false);
    this.layoutService.setDisplayHeader(false);
  }

  async login() {
    this.loading = true;
    try {
      let res = await this.userService.login({
        login: this.email,
        password: this.password
      });
      this.gaService.login(this.email);
      this.auth.updateInfo({token: res.access_token, account: res.account});
      this.auth.updateUser(res.user);

      if (!res.account) {
        this.loading = false;
      }

      await this.setupAndRedirect();
    } catch (e) {
      debug.error("login", e);
      toastr.error(e.message, "Đăng nhập thất bại!");
    }
    this.loading = false;
  }

  async setupAndRedirect() {
    try {
      let sessionInfo = await this.userService.checkToken();
      // fetch token for multi-account
      let accounts = sessionInfo.available_accounts.filter(e => e.type == "shop");
      accounts.sort((a, b) => a.id > b.id);
      for (let account of accounts) {
        let accountRes = await this.userService.switchAccount(account.id);
        account.token = accountRes.access_token;
        account.shop = accountRes.shop;
      }
      this.auth.updateAccounts(accounts);
      this.auth.updateShop(sessionInfo.shop, true);
      this.auth.selectAccount(0);
      let ordersCount = 0;
      let ref = this.auth.getRef(true);
      this.router.navigateByUrl(ref || (ordersCount >= 0 ? "fulfillments" : 'guide'));

    } catch (e) {
      debug.error("setupAndRedirect", e);
      toastr.error(e.message, "Đăng nhập thất bại!");
    }
  }

  async createAccount(shop) {
    try {
      shop.auto_create_ffm = true;
      let res = await this.userService.registerShop(shop);
      let id = res.shop.id;
      let accountRes = await this.userService.switchAccount(id);
      this.auth.updateInfo({token: accountRes.access_token, account: accountRes.account});
      this.auth.updateShop(accountRes.shop, true);
      // await this.setupAndRedirect();
      toastr.success("Tạo cửa hàng thành công.");
    } catch (e) {
      this.auth.clear();
      toastr.error(e.message, "Tạo cửa hàng thất bại");
    }
  }

  async doForgotPassword() {
    this.resetPasswordLoading = true;
    try {
      let email = this.formForgot.email;
      email = email && email.split(/-[0-9a-zA-Z]+-test/)[0] || '';
      email = email && email.split('-test')[0] || '';
      if (!validatecontraints.EmailValidates[0].check(email)) {
        throw new Error('Địa chỉ email không hợp lệ.');
      }

      await this.userService.requestResetPassword(this.formForgot.email);
      toastr.success("Một email với các chỉ dẫn đặt lại mật khẩu đã được gửi tới địa chỉ email của bạn nếu nó tồn tại trên hệ thống của eTop.");
      (<any>$("#modal-forgot-password")).modal("hide");
    } catch (e) {
      toastr.error(e.message, "Yêu cầu mật khẩu mới thất bại!");
    }

    this.resetPasswordLoading = false;
  }

  ngOnDestroy() {
    this.layoutService.setDisplayFooter(true);
    this.layoutService.setDisplayHeader(true);
  }
}



