import {AModel} from "./AModel";

export class District extends AModel {
  code: string;
  province_code: string;
  name: string;
}

export class Province extends AModel {
  code: string;
  name: string;
}

export class Ward extends AModel {
  code: string;
  district_code: string;
  name: string;
}



