import { Injectable } from '@angular/core';
import { HttpService } from "./http.service";
import List from "identical-list";
import { Subject } from "rxjs/Subject";
import { AuthenticateService } from "./authenticate.service";
import { District, Province, Ward } from "./models/location";
import { UtilService } from './util.service';
import { StorageService } from "./storage.service";

@Injectable()
export class LocationService {
  __service_key = "location";
  districtsList = new List<District>();
  provincesList = new List<Province>();
  wardsList = new List<Ward>();

  onProvinceUpdated = new Subject<List<Province>>();
  onDistrictUpdated = new Subject<List<District>>();
  onWardUpdated = new Subject<List<Ward>>();
  onLocationUpdated = new Subject();

  constructor(private auth: AuthenticateService,
    private http: HttpService,
    private storage: StorageService,
    private utilService: UtilService) {
    if (this.storage.ready) {
      this._loadData();
    }
    this.storage.onLocalStorageUpdated.subscribe(({ loaded }) => {
      if (loaded) {
        this._loadData();
      }
    })
    this.auth
    .authenInfo
    .distinctUntilKeyChanged('token')
    .subscribe(({ loaded }) => {
      if (!loaded) {
        this._init();
      }
    });
  }

  private async _loadData() {
    let data = this.storage.get(this.__service_key);
    if (data) {
      let { provinces, districts, wards } = data;
      this.districtsList = new List<District>(districts);
      this.onDistrictUpdated.next(this.districtsList);

      this.provincesList = new List<Province>(provinces);
      this.onProvinceUpdated.next(this.provincesList);

      this.wardsList = new List<Ward>(wards);
      this.onWardUpdated.next(this.wardsList);
    } else {
      return this._init();
    }
  }

  private async _init() {
    try {
      let districts = await this.loadDistricts();
      this.districtsList = new List<District>(districts);
      this.onDistrictUpdated.next(this.districtsList);

      let provinces = await this.loadProvinces();
      this.provincesList = new List<Province>(provinces);
      this.onProvinceUpdated.next(this.provincesList);

      let wards = await this.loadWards();
      this.wardsList = new List<Ward>(wards);
      this.onWardUpdated.next(this.wardsList);

      this.storage.set(this.__service_key, {
        provinces: provinces,
        districts: districts,
        wards: wards
      })
    } catch (e) {
    }
    this.onLocationUpdated.next();
  }


  private loadDistricts(): Promise<Array<District>> {
    return this.http.post('/api/etop.Location/GetDistricts', {}).toPromise()
      .then(res => this.sortByName(res.districts.map(d => new District(Object.assign({ id: d.code }, d)))));
  }

  private loadProvinces(): Promise<Array<Province>> {
    return this.http.post('/api/etop.Location/GetProvinces', {}).toPromise()
      .then(res => {
        let provinces = this.sortProvinces(res.provinces.map(p => new Province(Object.assign({ id: p.code }, p))));
        this.provincesList = new List<Province>(provinces);
        return provinces;
      });
  }

  private loadWards(): Promise<Array<Ward>> {
    return this.http.post('/api/etop.Location/GetWards', {}).toPromise()
      .then(res => this.sortByName(res.wards.map(w => new Ward(Object.assign({ id: w.code }, w)))));
  }

  async getProvinces() {
    if (this.provincesList.length) {
      return this.provincesList.asArray();
    } else {
      return this.loadProvinces();
    }
  }

  getDistrictById(id: number) {
    return this.districtsList.get(id);
  }

  getProvinceById(id: number) {
    return this.provincesList.get(id);
  }

  getWardById(id: number) {
    return this.wardsList.get(id);
  }

  async getDistrictsByProvince({ province_code, province_name }): Promise<Array<District>> {
    if (this.districtsList.length) {
      return this.districtsList.filter(d => d.province_code == province_code);
    } else {
      return this.http.post("/api/etop.Location/GetDistrictsByProvince", { province_code, province_name }).toPromise()
        .then(res => this.sortByName(res.districts.map(d => new District(Object.assign({ id: d.code }, d)))));
    }
  }

  async getWardsByDistrict({ district_code, district_name }): Promise<Array<Ward>> {
    if (this.wardsList.length) {
      return this.wardsList.filter(w => w.district_code == district_code);
    } else {
      return this.http.post("/api/etop.Location/GetWardsByDistrict", { district_code, district_name }).toPromise()
        .then(res => this.sortByName(res.wards.map(w => new District(Object.assign({ id: w.code }, w)))));
    }
  }

  sortProvinces(provinces) {
    provinces.forEach(p => p.name = p.name.replace(/Tỉnh |Thành phố /g, ''));
    provinces.sort((a, b) => this.utilService.removeDiacritic(a.name) > this.utilService.removeDiacritic(b.name) ? 1 : -1);
    let priorities = ['79', '01', '48', '92'];
    let priProvinces = provinces.filter(p => {
      switch (p.code) {
        case '79':
          p.priority = 1;
          return true;
        case '01':
          p.priority = 2;
          return true;
        case '48':
          p.priority = 3;
          return true;
        case '92':
          p.priority = 4;
          return true;
        default:
          return false;
      }
    });
    provinces = provinces.filter(p => priorities.indexOf(p.code) < 0);
    priProvinces.sort((a, b) => a.priority > b.priority ? 1 : -1)

    provinces = priProvinces.concat(provinces);

    return provinces;
  }

  sortByName(data) {
    return data.sort((a, b) => this.utilService.removeDiacritic(a.name) > this.utilService.removeDiacritic(b.name) ? 1 : -1);
  }
}



