import {AModel} from "./AModel";
import {OrderAddress, ShopShippingAddress} from "./address";
import {Fulfillment} from "./fulfillment";
import {Shop} from "./account";

export class Order extends AModel {
  id: string;
  token: string;
  shop_id: string;
  shop_name: string;
  code: string;
  source: string;
  payment_method: string;
  created_at: string;
  processed_at: string;
  updated_at: string;
  closed_at: string;
  confirmed_at: string;
  cancelled_at: string;
  cancel_reason: string;
  created_by: string;
  updated_by: string;
  confirmed_by: string;
  cancelled_by: string;
  sh_confirm: string;
  confirm: string;
  status: string;
  status_display: string;
  fulfillment_status: string;
  etop_payment_status: string;

  total_items: number;
  basket_value: number;
  total_weight: number;
  total_discount: number;
  total_amount: number;
  order_note: string;
  shipping_note: string;
  shop_shipping_fee: number;
  shop_cod: number;
  reference_url: string;
  ghn_note_code: string;

  payment_status_display: string;
  source_display: string;

  ghn_note_display: string;

  status_mapped: {
    display: string,
    state: string
  };

  discount: Discount;

  customer: any;
  customer_address: OrderAddress;
  billing_address: OrderAddress;
  shipping_address: OrderAddress;
  shop_shipping: ShopShippingAddress;

  fulfillments: Array<Fulfillment>;
  lines: Array<OrderLine>;

  shop: Shop;

  o_data: any;
  p_data: any = {};
}

export class OrderLine {
  order_id: string;
  variant_id: string;
  supplier_id: string;
  x_variant_id: string;
  product_name: string;
  product_exists: boolean;
  updated_at: string;
  closed_at: string;
  confirmed_at: string;
  cancelled_at: string;
  cancel_reason: string;
  s_confirm: string;
  quantity: number;
  list_price: number;
  retail_price: number;
  payment_price: number;
  image_url: string;
  attributes: Array<any>;

  o_data: any;
  p_data: any;
}

export class Discount {
  code: string;
  type: string;
  amount: number;
}



