import {AModel} from "./AModel";

export class BankAccount {
  account_name: string;
  account_number: string;
  branch: string;
  name: string;
  province: string;
}

export class Bank extends AModel {
  code: string;
  name: string;
  type: string;
}

export class BankProvince extends AModel {
  code: string;
  name: string;
  bank_code: string;
}

export class BankBranch extends AModel {
  code: string;
  name: string;
  bank_code: string;
  province_code: string;
}



