import {Injectable} from '@angular/core';
import {HttpService} from "./http.service";
import * as query from "query-string";
import {AService} from "./aservice.service";
import {SocketService} from "./socket.service";
import {AuthenticateService} from "./authenticate.service";

export class AccountModel {
  name: string;
  address?: ShopModelAddress;
  phone?: string;
  bank_account?: ShopModelBank;
  website_url?: string;
  logo_url?: string;
  email?: string;
  auto_create_ffm?: boolean;
  ghn_note_code?: string;
}

export class ShopModelBank {
  name?: string;
  province?: string;
  branch?: string;
  account_number?: string;
  account_name?: string;
}

export class ShopModelAddress {
  id?: string;
  province?: string;
  province_code?: string;
  district?: string;
  district_code?: string;
  ward?: string;
  ward_code?: string;
  address?: string;
}

@Injectable()


export class UserService {
  constructor(public http: HttpService,
              private socketService: SocketService,
              private auth: AuthenticateService) {
  }

  async getProfile() {
    return this.http.get("/user").toPromise();
  }

  async checkToken(token?: string) {
    return this.http.post("api/etop.User/SessionInfo", {}).toPromise();
  }

  async update(user) {
    return this.http.put("/user", user).toPromise();
  }

  async login(data) {
    if (!data.login) {
      throw new Error("Missing email");
    }

    if (!data.password) {
      throw new Error("Missing password");
    }

    if (!data.account_type) {
      data.account_type = 'shop';
    }

    return this.http.post("/api/etop.User/Login", data).toPromise();
  }

  async signUp(data) {
    if (!data.full_name) {
      throw new Error("Vui lòng điền tên đầy đủ");
    }
    if (!data.phone) {
      throw new Error("Vui lòng điền số điện thoại");
    }
    if (!data.email) {
      throw new Error("Vui lòng nhập địa chỉ email");
    }
    if (!data.password) {
      throw new Error("Vui lòng điền mật khẩu");
    }
    return this.http.post("api/etop.User/Register", data).toPromise();
  }

  registerShop(data: AccountModel) {
    return this.http.post('api/shop.Account/RegisterShop', data).toPromise();
  }

  updateShop(data: AccountModel) {
    return this.http.post('api/shop.Account/UpdateShop', data).toPromise();
  }

  updateShopField(data) {
    return this.http.post('api/shop.Account/UpdateShop', data).toPromise();
  }

  updateBankAccount(data, token) {
    return this.http.purePost('api/shop.Account/UpdateShop', data, token).toPromise();
  }

  deleteShop(id: string) {
    return this.http.post(`api/shop.Account/DeleteShop`, {"id": id}).toPromise();
  }

  switchAccount(account_id: string) {
    return this.http.post(`api/etop.User/SwitchAccount`, {account_id}).toPromise();
  }

  changePassword({email, current_password, new_password, confirm_password}) {
    return this.http.post(`api/etop.User/ChangePassword`, {
      email, current_password, new_password, confirm_password
    }).toPromise();
  }

  requestResetPassword(email) {
    return this.http.post("api/etop.User/ResetPassword", {email: email}).toPromise();
  }

  setupEtopApp(data) {
    return this.http.post("api/shop.Haravan/ConnectHaravan", data).toPromise();
  }

  updateEtopApp(data) {
    return this.http.post(`api/shop.Haravan/UpdateHaravanToken`, data).toPromise();
  }

  async getProvider(): Promise<any> {
    return {};
    // return this.http.post(`api/shop.Account/GetProviders`, {}).toPromise()
    //   .catch((e) => {
    //     debug.error("[UserService:getProvider]", e);
    //     return {};
    //   });
  }

  setDefaultAddress(address_id, type, token?) {
    if (token) {
      let headers = this.http.createHeader(token);
      let options = this.http.createDefaultOption(headers);
      return this.http.postWithOptions(`api/shop.Account/SetDefaultAddress`, {
        id: address_id,
        type: type
      }, options).toPromise();
    }

    return this.http.post(`api/shop.Account/SetDefaultAddress`, {
      id: address_id,
      type: type
    }).toPromise();
  }

  updateURLSlug(account_id, url_slug) {
    return this.http.post("api/etop.Account/UpdateURLSlug", {
      account_id,
      url_slug
    }).toPromise();
  }

  changePasswordUsingToken(data) {
    return this.http.post("api/etop.User/ChangePasswordUsingToken", data).toPromise();
  }

  updatePassword(data) {
    return this.http.post("api/etop.User/ChangePassword", data).toPromise();
  }

  upgradeAccessToken(stoken) {
    return this.http.post("api/etop.User/UpgradeAccessToken", { stoken }).toPromise();
  }

  sendEmailVerification(email) {
    return this.http.post("api/etop.User/SendEmailVerification", { email }).toPromise();
  }

  sendPhoneVerification(phone) {
    return this.http.post("api/etop.User/SendPhoneVerification", { phone }).toPromise();
  }

  verifyEmailUsingToken(verification_token) {
    return this.http.post("api/etop.User/VerifyEmailUsingToken", { verification_token }).toPromise();
  }

  verifyPhoneUsingToken(verification_token) {
    return this.http.post("api/etop.User/VerifyPhoneUsingToken", { verification_token }).toPromise();
  }

  authorizeShop() {
    return this.http.post("api/partner.Shop/AuthorizeShop", {}).toPromise();
  }

  initIntergration(auth_token: string) {
    return this.http.post("api/integration.Integration/Init", {
      auth_token
    }).toPromise();
  }

  requestLogin(login, token, captcha) {
    let header = this.http.createHeader(token);
    let options = this.http.createDefaultOption(header);
    return this.http.postWithOptions("api/integration.Integration/RequestLogin", {
      login,  recaptcha_token : captcha
    }, options).toPromise();
  }

  loginUsingToken(login, verification_code, token) {
    let header = this.http.createHeader(token);
    let options = this.http.createDefaultOption(header);
    return this.http.postWithOptions("api/integration.Integration/LoginUsingToken", {
      login, verification_code
    }, options).toPromise();
  }

  grantAccess(shop_id: string) {
    return this.http.post('api/integration.Integration/GrantAccess', {
      shop_id
    }).toPromise();
  }
}



