import { Injectable } from '@angular/core';

@Injectable()
export class LayoutService {
  displayHeader = true;
  displayFooter = true;
  constructor() { }

  setDisplayHeader(value: boolean) {
    this.displayHeader = value;
  }
  setDisplayFooter(value: boolean) {
    this.displayFooter = value;
  }
}



