import {Injectable} from '@angular/core';
import {HttpService} from "./http.service";
import {Subject} from "rxjs/Subject";
import List from "identical-list";
import {ShopService} from "./shop.service";
import {UtilService} from "./util.service";
import {Product, Variant} from "./models/product";
import {Brand} from "./models/brand";
import { TimerService } from './timer.service';


@Injectable()
export class ProductService {
  onProductServiceReady = new Subject();

  onProductListUpdated = new Subject<List<Product>>();
  productList = new List<Product>();
  allProducts = new List<Product>();
  excludeList = [];
  brandsList = new List<Brand>();

  private _isLoading = true;
  get isLoading() {
    return this._isLoading;
  }

  set isLoading(loading) {
    if (loading != this._isLoading && loading == false) {
      this.onProductServiceReady.next();
    }
    this._isLoading = loading;
  }

  constructor(private http: HttpService,
              private shopService: ShopService,
              private util: UtilService,
              private timerService: TimerService) {
    // this.init();

    window['__productService'] = this;

    this.fetchBrands();
  }

  async init() {

    let limit = 100;
    let refreshProduct = async () => {
      await this._loadProduct(limit);
      limit = 10000;
      this.timerService.setTimeout(refreshProduct, 30000, 'GET_PRODUCT', true);
    };
    this.excludeList = this.shopService.productIDList.map(p => p.id);
    this.shopService.onProductIDListUpdated.subscribe(async (list: List<any>) => {
      this.excludeList = list.map(p => p.id);
      this.productList = new List(this.productList.filter(p => this.excludeList.indexOf(p.id) < 0));
      this.onProductListUpdated.next(this.productList);
    });
    refreshProduct();
  }

  private async _loadProduct(limit) {
    this.isLoading = true;
    try {
      let products = await this.loadProduct({paging: {limit: limit}});
      this.productList.concat(products);
      this.allProducts = this.productList;
      this.productList = new List(this.productList.filter(p => this.excludeList.indexOf(p.id) < 0));
      this.onProductListUpdated.next(this.productList);
    } catch (e) {
      debug.log('error', e);
    }
    this.isLoading = false;
  }

  getLoadStatus() {
    return this.isLoading;
  }

  fetchBrands() {
    return this.http.post("api/shop.Brand/GetBrands", {}).toPromise()
      .then(res => {
        let brands = res['brands'];
        brands = brands.sort((a, b) => (a.name < b.name) ? -1 : 1);
        this.brandsList = new List<Brand>(brands);
        return brands;
      })
      .catch((e) => {
        debug.error("[ProductService:fetchBrands]", e);
        return [];
      });
  }

  getBrand(id): Brand {
    let brand = this.brandsList.get(id);
    return brand || null;
  }

  async getBrandByProductId(productId) {
    let product = this.allProducts.get(productId);
    if (!product) {
      product = await this.getProduct(productId);
    }

    let brand = this.getBrand(product.brand_id);
    return (brand && brand.name) || '';
  }

  private _productMap(p): Product {
    let v = p.variants[0];
    let product = new Product({
      id: p.id,
      name: p.name,
      price: v.list_price,
      wholesale_price: v.wholesale_price,
      list_price_min: v.retail_price_min,
      list_price_max: v.retail_price_max,
      quantity_available: p.quantity_available,
      image: `url(${p.image_urls[0] || "assets/images/placeholder_2.png"})`,
      image_urls: p.image_urls,
      status: p.status,
      brand_id: p.brand_id,
      collection_ids: p.collection_ids,
      category_id: p.category_id,
      brand_info: this.getBrand(p.s_brand_id),
      variants: p.variants.map(variant => this._variantMap(p.name, variant, {image: p.image_urls[0]})),
      o_data: p,
      desc_html: v.desc_html,
      short_desc: v.short_desc,
      attributes: (p.attributes && p.attributes.length > 0) ? p.attributes : p.x_attributes,
      product_source_id: p.product_source_id,
      product_source_name: p.product_source_name,
      product_source_type: p.product_source_type
    });

    let oldProduct = this.productList.get(p.id);
    if (oldProduct) {
      product.p_data = oldProduct.p_data;
    }

    return product;
  }

  private _variantMap(product_name, v, variantDefault = {image: "assets/images/placeholder_2.png"}): Variant {
    return new Variant({
      id: v.id,
      name: product_name,
      list_price: v.list_price,
      retail_price: v.retail_price,
      wholesale_price: v.wholesale_price,
      list_price_min: v.retail_price_min,
      list_price_max: v.retail_price_max,
      quantity_available: v.quantity_available,
      status: v.status,
      image_urls: v.image_urls,
      image_url: `url(${v.image_urls[0] || variantDefault.image || "assets/images/placeholder_2.png"})`,
      o_data: v,
      desc_html: v.desc_html,
      short_desc: v.short_desc,
      attributes: (v.attributes && v.attributes.length > 0) ? v.attributes : v.x_attributes,
    });
  }

  loadProduct(query?: any): Promise<Array<Product>> {
    return this.http.post(`api/shop.Browse/BrowseProducts`, query || {}).toPromise()
      .then((res) => {
        res.products.sort(function (a, b) {
          let x: any = new Date(b.updated_at);
          let y: any = new Date(a.updated_at);
          return x - y;
        });

        return res.products.map(this._productMap.bind(this));
      })
      .catch((e) => {
        debug.error("[ProductService:loadProduct]", e);
        return [];
      });
  }

  getProduct(id): Promise<Product> {
    return this.http.post(`api/shop.Browse/BrowseProduct`, {'id': id}).toPromise()
      .then((res) => {
        return this._productMap(res.product);
      });
  }

  listProduct(start?: number, end?: number, filters?: Array<any>): Array<Product> {
    let subset = this.productList.asArray();
    if (filters instanceof Array) {
      for (let filter of filters) {
        subset = subset.filter(p => {
          switch (filter.name_rule) {
            case 'product_name': {
              return this.util.searchCompare(p.name, filter.value, filter.compair_rule);
            }
            case 'wholesale_price': {
              return eval('p.' + filter.name_rule + filter.compair_rule + filter.value);
            }
            case 'list_price': {
              return eval('p.' + filter.name_rule + filter.compair_rule + filter.value);
            }
          }
        });
      }
    }
    return subset.slice(start || 0, end || subset.length);
  }

  addTags(productIds, tags) {
    return this.http.post("api/shop.Product/UpdateProductsTags", {ids: productIds, adds: tags}).toPromise();
  }

  filter(data) {
    return this.http.post("/product/filter", data).toPromise();
  }

  deleteProduct(ids: Array<string>) {
    return this.http.post("api/shop.Product/RemoveProducts", {ids}).toPromise();
  }
}



