import {Injectable} from "@angular/core";
import {StorageService} from "./storage.service";
import {Subject} from "rxjs/Subject";
import misc from "../../libs/misc";
import {Account, Shop} from "./models/account";

// import {SocketService} from "../shared-services/socket.service";
export class AuthenticateInfoParams {
  token: string;
  account?: any;
  shop?: Shop;
  user?: any;
}

@Injectable()
export class AuthenticateService {
  __storage_key = "auth-data";
  user: any = null;
  account: Account | any = null;
  shop: Shop = null;

  accounts = [];

  token = "";
  __ref = "";


  public authenInfo: Subject<any> = new Subject();

  constructor(private storage: StorageService) {
    storage.onLocalStorageUpdated.subscribe(({data, loaded}) => {
      let authData = data[this.__storage_key];
      if (loaded && authData) {
        this.user = authData.user;
        this.account = authData.account;
        this.token = authData.token;
        this.shop = authData.shop;
        this.accounts = authData.accounts;
        this._invokeUpdate();
      }
    });

    if (storage.ready) {
      let authData = storage.get(this.__storage_key);
      if (authData) {
        this.user = authData.user;
        this.account = authData.account;
        this.token = authData.token;
        this.shop = authData.shop;
        this.accounts = authData.accounts;
        this._invokeUpdate(true);
      }
    }
  }

  private _invokeUpdate(loaded = false) {
    this.authenInfo.next({
      token: this.token,
      account: this.account,
      shop: this.shop,
      user: this.user,
      accounts: this.accounts,
      loaded: loaded
    });
  }

  updateInfo(info: AuthenticateInfoParams) {
    this.token = info.token;
    this.account = info.account || this.account;
    this.shop = info.shop || this.shop;
    this.user = info.user || this.user;
    this.store();
    this._invokeUpdate();
  }

  updateUser(user) {
    this.user = user;
    this.store();
    this._invokeUpdate();
  }

  updateToken(token) {
    this.token = token;

    this.store();
    this._invokeUpdate();
  }

  updateAccount(account) {
    const accIndex = this.findAccountIndex(account.id);
    this.accounts[accIndex] = account;
    this.account = account;
    this.store();
    this._invokeUpdate();
  }

  updateSToken(token) {
    this.shop.token = token;
    this.store();
  }

  updateShop(shop, force = false) {
    let account = this.accounts.find(a => a.id == shop.id);

    if (account) {
      account.shop = shop;
      account.name = shop.name;
      account.image_url = shop.image_url;
    }
    if ((this.shop && this.shop.id == shop.id) || force) {
      this.shop = shop;
      this.account = account;
    }
    this.store();
    this._invokeUpdate();
  }

  setDefaultAddress(address_id, type, shop_id?) {
    let shop = this.shop;

    if (shop_id) {
      let acc = this.accounts.find(a => a.shop.id == shop_id);
      if (acc && acc.shop) {
        shop = acc.shop;
      }
    }

    if (type == 'shipfrom') {
      shop.ship_from_address_id = address_id;
    } else if (type == 'shipto') {
      shop.ship_to_address_id = address_id;
    }

    this.store();
    this._invokeUpdate();
  }

  updateAutoFfmOption(option) {
    this.shop.auto_create_ffm = option;
    this.store();
    this._invokeUpdate();
  }

  updateGhnNoteCode(code) {
    this.shop.ghn_note_code = code;
    this.store();
    this._invokeUpdate();
  }

  updateProductSrcId(id) {
    this.shop.product_source_id = id;
    this.store();
    this._invokeUpdate();
  }

  addAccount(account) {
    let existed = this.accounts.find(a => a.id == account.id);
    if (existed) {
      return this.updateShop(account);
    }
    this.accounts.push(account);
    this.store();
    this._invokeUpdate();
  }

  removeShop(shop_id) {
    let accounts = this.accounts.filter(a => a.id != shop_id);
    this.accounts = accounts;
    this.store();
    this._invokeUpdate();
  }


  updateAccounts(accounts: [any]) {
    this.accounts = accounts;
    this._invokeUpdate();
    this.store();
  }

  selectAccount(index): boolean {
    let account = this.accounts.find(a => a.url_slug == index);
    if (!account) {
      if (index < 0 || index >= this.accounts.length) {
        return false;
      }
      account = this.accounts[index];
    }

    if (!account) {
      return false;
    }

    if (this.account.id == account.id) {
      return true;
    }
    this.token = account.token;
    this.account = account;
    this.shop = account.shop;
    this._invokeUpdate();
    this.store();
    return true;
  }

  currentAccountIndex() {
    return this.findAccountIndex(this.account.id);
  }

  findAccountIndex(id) {
    return this.accounts.findIndex(a => a.id === id);
  }

  setRef(url) {
    this.__ref = url;
  }

  getRef(clear = false) {
    const ref = this.__ref;
    if (clear) {
      this.__ref = "";
    }
    return ref;
  }

  store() {
    this.storage.set(this.__storage_key, {
      token: this.token,
      user: this.user,
      account: this.account,
      shop: this.shop,
      accounts: this.accounts,
    });
  }

  clear() {
    this.token = "";
    this.account = {};
    this.shop = null;
    this.user = {};
    this.accounts = [];
    this.storage.set(this.__storage_key, null);
    this.authenInfo.next({
      token: "",
      account: null,
      shop: null,
      user: null
    });
  }

  getTokenByShop(shop_id) {
    const shop = this.accounts.find(a => a.id === shop_id);
    return shop ? shop.token : null;
  }

  getStringTokenByShop(shop_id) {
    return misc.encodeBase64(this.getTokenByShop(shop_id));
  }
}



