import {enableProdMode} from '@angular/core';
import {platformBrowserDynamic} from '@angular/platform-browser-dynamic';
import 'moment/locale/vi';
import {environment} from './mobile-environments/environment';
import "libs/debug";
import * as queryString from "query-string";
import {hmrBootstrap} from './hmr';
import { MobileModule } from './app/mobile/mobile.module';

const parsed = queryString.parse(location.search);
(window as any).__debug_host = environment.base_url;
// (window as any).__debug_host = parsed.debug_host;

if (environment.production) {
  enableProdMode();
}

const bootstrap = () => platformBrowserDynamic().bootstrapModule(MobileModule);
if (environment.hmr) {
  if (module[ 'hot' ]) {
    hmrBootstrap(module, bootstrap);
  } else {
    console.error('HMR is not enabled for webpack-dev-server!');
    console.log('Are you using the --hmr flag for ng serve?');
  }
} else {
  bootstrap();
}



