import { AModel } from "./AModel";

export class ProductSrc extends AModel {
    id: string;
    type: string;
    name: string;
    status: string;
    updated_at: string;
    created_at: string;
}

export class ProductSrcVariant extends AModel {
    product_source_id: string;
    product_id: string;
    product_name: string;
    name: string;
    description: string;
    short_desc: string;
    desc_html: string;
    image_urls: Array<string>;
    tags: Array<string>;
    status: string;
    list_price: number;
    cost_price: number;
    sku: string;
    code: string;
    quantity_available: number;
    quantity_on_hand: number;
    quantity_reserved: number;
    attributes: Array<any>;
    checked?: boolean;
    retail_price?: number;
    price?: number;
}


