import { ApplicationRef, CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { RouterModule, Routes } from '@angular/router';
import { SharedServiceModule } from '../shared-services/shared-services.module';
import { FormsModule } from '@angular/forms';
import { IframeService } from '../shared-services/iframe.service';
import { MobileRoutes } from './mobile.route';

import 'rxjs/add/operator/takeUntil';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import 'rxjs/add/operator/distinctUntilKeyChanged';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../../environments/environment';
import { HttpClientModule } from '@angular/common/http';
import { SharedPipeModule } from '../shared-pipes/shared-pipes.module';
import { MobileComponent } from './mobile.component';
import { ScreenModule } from './screens/screens.module';
import { ComponentsModule } from './components/components.module';
import { LayoutService } from './services/layout.service';

const routes: Routes = MobileRoutes;

const components = [MobileComponent];

const services = [IframeService, LayoutService];

@NgModule({
  declarations: [...components],
  imports: [
    CommonModule,
    HttpClientModule,
    SharedServiceModule,
    ScreenModule,
    ComponentsModule,
    SharedPipeModule,
    BrowserModule,
    FormsModule,
    environment.production ? ServiceWorkerModule.register('ngsw-worker.js') : [],
    RouterModule.forRoot(routes)
  ],
  providers: [...services],
  bootstrap: [MobileComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class MobileModule {
  constructor(public appRef: ApplicationRef) {}
}



