import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import List from 'identical-list';
import { Subject } from 'rxjs/Subject';
import { AuthenticateService } from './authenticate.service';
import { Bank, BankBranch, BankProvince } from './models/bank';

@Injectable()
export class BankService {
  bankList = new List<Bank>();
  onBankUpdated = new Subject<List<Bank>>();

  constructor(private auth: AuthenticateService, private http: HttpService) {
    this._init();
    this.auth.authenInfo.subscribe(() => {
      this._init();
    });
  }

  private async _init() {
    try {
      const banks = await this.loadBanks();
      this.bankList = new List<Bank>(this.sortBanks(banks));
      this.onBankUpdated.next(this.bankList);
    } catch (e) {}
  }

  loadBanks(): Promise<Array<Bank>> {
    return this.http
      .post('api/etop.Bank/GetBanks', {})
      .toPromise()
      .then(res => res.banks.map(bank => new Bank(Object.assign({ id: bank.code }, bank))));
  }

  getBanks(): List<Bank> {
    return this.bankList;
  }

  getProvinceByBank({ bank_code, bank_name }): Promise<Array<BankProvince>> {
    return this.http
      .post('api/etop.Bank/GetProvincesByBank', {
        bank_code,
        bank_name
      })
      .toPromise()
      .then(res =>
        res.provinces.map(
          province => new BankProvince(Object.assign({ id: province.code }, province))
        )
      );
  }

  getBankBranches({
    bank_code,
    bank_name,
    province_code,
    province_name
  }): Promise<Array<BankBranch>> {
    return this.http
      .post('api/etop.Bank/GetBranchesByBankProvince', {
        bank_code,
        bank_name,
        province_code,
        province_name
      })
      .toPromise()
      .then(res =>
        res.branches.map(branch => new BankBranch(Object.assign({ id: branch.code }, branch)))
      );
  }
  sortBanks(banks) {
    const priorities = [
      { i: 0, n: '301' },
      { i: 1, n: '201' },
      { i: 2, n: '304' },
      { i: 3, n: '303' },
      { i: 4, n: '310' },
      { i: 5, n: '204' },
      { i: 6, n: '307' },
      { i: 7, n: '358' },
      { i: 8, n: '202' }
    ];
    banks.sort((a, b) => a.name > b.name);

    const priBanks = [];
    const norBanks = [];

    banks.forEach(b => {
      const prior = priorities.find(p => p.n === b.code);
      if (!prior) {
        norBanks.push(b);
        return;
      }
      priBanks[prior.i] = b;
    });

    return priBanks.concat(norBanks);
  }
}



