import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'status'
})
export class StatusPipe implements PipeTransform {
  statusHash: any = {
    confirmed: 'Đã xác nhận',
    new: 'Mới',
    processing: 'Đang xử lý',
    delivering: 'Đang giao hàng',
    unpaid: 'Chưa thánh toán',
    paid: 'Đã thanh toán',
    successful: 'Thành công',
    completed: 'Hoàn tất',
    canceled: 'Hủy'
  };

  transform(value: string): string {
    return this.statusHash[value] || 'Không xác định';
  }
}

@Pipe({
  name: 'statusvn'
})
export class StatusVN implements PipeTransform {
  statusHash: any = {
    pending: 'Đang chờ',
    published: 'Đã đồng bộ',
    new: 'Mới',
    stopped: 'Ngưng bán',
    'error': 'Đồng bộ thất bại'
  };

  transform(value: string): string {
    return this.statusHash[value] || '';
  }
}
@Pipe({
  name: 'statusorder'
})
export class StatusOrder implements PipeTransform {
  statusHash: any = {
    P: 'Đã xác Nhận',
    N: 'Từ Chối',
    Z: 'Mới',
  };

  transform(value: string): string {
    return this.statusHash[value] || value;
  }
}

@Pipe({
  name: 'vnd'
})
export class VND implements PipeTransform {
  transform(value: any): string {
    if (typeof value != "number" && typeof value != "string") {
      return value;
    }
    if (typeof value == "number") {
      if (value < 0) {
        return '-' + (-value).toFixed()
          .replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
      }
      return value.toFixed()
        .replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
    }
    if (typeof value == "string") {
      return value.replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
    }
    return value;
  }
}



