import {Injectable} from '@angular/core';
import * as io from 'socket.io-client';
import {AuthenticateService} from './authenticate.service';
import {environment} from '../../environments/environment';
import {Subject} from "rxjs/Subject";

@Injectable()
export class SocketService {
  private _socket: any;
  private _listener = {};
  jwt: any;


  onNewMessage = new Subject<any>();

  constructor(private authenService: AuthenticateService) {
    try {
      // this.jwt = authenService.token;
      // authenService.authenInfo.subscribe(info => {
      //   const oldToken = this.jwt;
      //   this.jwt = info.token;
      //   if (oldToken !== info.token && !!info.token) {
      //     this.connectSocket();
      //   }
      // });
      // this.connectSocket();

    } catch (e) {
      debug.error('Kết nối _socket thất bại : ', e.stack);
    }
  }

  register(event, cb) {
    if (!this._listener[event]) {
      this._listener[event] = [];
      this._socket.on(event, (data) => {
        if (!environment.production) {
          debug.log(event, data);
        }
        this._listener[event].forEach(callback => {
          callback(data);
        });
      });
    }
    this._listener[event].push(cb);
  }

  connectSocket() {
    this._socket = io(`/`);
    this._listener = {};
    this._socket.on('connect', async (_socket) => {
      if (!environment.production) {
        debug.log("Connect to server succesful ", this._socket.id);
      }
      const rs = await this.emit('login', {});
    });

    this.register("MESSAGE_CREATED", (data) => {
      this.onNewMessage.next(data);
    });
  }

  disconnectSocket() {
    if (this._socket) {
      this._socket.disconnect();
    }
  }

  on(code, cb) {
    this._socket.on(code, cb);
  }

  emit(code, data) {
    data['token'] = this.jwt;
    return new Promise((resolve, reject) => {
      this._socket.emit(code, data, (res) => {
        if (!environment.production) {
          debug.log(code, data, res);
        }
        if (res.success) {
          resolve(res);
        } else {
          reject(res.error);
        }
      });
    });
  }
}



