import {Pipe, PipeTransform} from '@angular/core';

@Pipe({
  name: 'createRange'
})
export class CreateRangePipe implements PipeTransform {

  transform(value: number, args?: any): number[] {
    let arr = [];
    for (let i = 1; i <= value; i++) {
      arr.push(i);
    }
    return arr;
  }

}



