import { Injectable } from '@angular/core';

export class NotiOptions {
  title?: string;
}

@Injectable()
export class NotiService {

  constructor() {
    toastr.options = Object.assign(toastr.options, {

    });
  }

  public success(message: string, opts?: NotiOptions) {
    return toastr.success(message, opts.title || null, opts);
  }

  public info(message: string, opts?: NotiOptions) {
    return toastr.info(message, opts.title || null, opts);
  }

  public warning(message: string, opts?: NotiOptions) {
    return toastr.warning(message, opts.title, opts);
  }

  public error(message: string, opts?: NotiOptions) {
    return toastr.error(message, opts.title, opts);
  }

}



