import { Injectable } from '@angular/core';
import { HttpService } from "./http.service";
import { Subject } from "rxjs/Subject";
import * as moment from 'moment';
import { Fulfillment } from "./models/fulfillment";
import { OrderAddress } from "./models/address";
import { LocationService } from "./location.service";
import { UtilService } from "./util.service";
import { AuthenticateService } from "./authenticate.service";
import List from 'identical-list';

interface ForceItem {
  id: string;
  type: string;
  expireTime: number;
}

const providerLogo = {
  ghn: "ghn-l.png",
  ghtk: "ghtk-l.png",
  vtpost: "vtpost-l.png",
  default: "placeholder_3.png",
};

const providerLogoSm = {
  ghn: "ghn-s.png",
  ghtk: "ghtk-s.png",
  vtpost: "vtpost-s.png",
  default: "placeholder_3.png",
};

const shippingFeeShopMap = {
  main: "Phí giao hàng",
  return: "Phí trả hàng",
  adjustment: "Phí vượt cân",
  cods: "Phí thu hộ (COD)",
  insurance: "Phí bảo hiểm",
  address_change: "Phí đổi thông tin",
  discount: "Giảm giá",
  other: "Phụ phí khác"
};

const fullfillmentStatus = {
  new: {
    display: "Mới",
    state: "new"
  },
  default: {
    display: "Đã tạo",
    state: "created"
  },
  created: {
    display: "Đã tạo",
    state: "created"
  },
  confirmed: {
    display: "Đã xác nhận",
    state: "confirmed"
  },
  picking: {
    display: "Đang lấy hàng",
    state: "picking"
  },
  holding: {
    display: "Chờ giao",
    state: "holding"
  },
  delivering: {
    display: "Đang giao",
    state: "delivering"
  },
  delivered: {
    display: "Đã giao",
    state: "delivered"
  },
  returning: {
    display: "Đang trả hàng",
    state: "returning"
  },
  returned: {
    display: "Đã trả hàng",
    state: "returned"
  },
  cancelled: {
    display: "Hủy",
    state: "cancelled"
  },
  error: {
    display: "Lỗi vận đơn",
    state: "error"
  },
  unknown: {
    display: "Không xác định",
    state: "unknown"
  }
};

@Injectable()
export class FulfillmentService {

  ready = false;
  onReady = new Subject();

  forceList: List<ForceItem>;

  private __secretCode = "fulfillment-data";

  constructor(private locationService: LocationService,
    private auth: AuthenticateService,
    private util: UtilService,
    private http: HttpService) {
      this.getLocalForceList();
  }

  public fulfillmentMap(fulfillment): Fulfillment {
    let f = new Fulfillment(Object.assign({}, fulfillment, {
      address_to: this._locationMap(fulfillment.address_to),
      o_data: fulfillment
    }));
    f.address_from = f.address_from || this._locationMap(f.order && f.order.shop_shipping && f.order.shop_shipping.sh_address);
    f.status_mapped = this._statusMap(f);
    const { hash, mappedLines } = this._shippingFeeShopMap(f.shipping_fee_shop_lines);
    f.shipping_fee_shop_lines = mappedLines;
    f.shipping_fee_shop_hash = hash;
    return f;
  }

  private _locationMap(address): OrderAddress {
    if (!address) {
      return null;
    }
    return new OrderAddress(Object.assign({}, address, {
      o_data: address,
      Province: this.locationService.getProvinceById(address.province_code),
      District: this.locationService.getDistrictById(address.district_code),
      Ward: this.locationService.getWardById(address.ward_code)
    }));
  }

  private _statusMap(ffm) {
    const { status, shipping_state, x_sync_status } = ffm;
    switch (status) {
      case "N":
        return fullfillmentStatus.cancelled;
      case "Z":
        return x_sync_status === 'N' ? fullfillmentStatus.error : fullfillmentStatus.new;
    }
    return fullfillmentStatus[shipping_state];
  }

  private _shippingFeeShopMap(lines) {
    const hash: any = {};
    const mappedLines = lines.map(line => {
      const { external_service_name, shipping_fee_type, cost } = line;
      const name = shippingFeeShopMap[shipping_fee_type] || external_service_name;
      hash[shipping_fee_type] = cost;
      return { ...line, name };
    });
    return { hash, mappedLines };
  }

  loadFulfillments(paging = {limit: 10000}) {
    return this.http.post("api/shop.Fulfillment/GetFulfillments", {
      paging
    }).toPromise()
      .then(res => res.fulfillments.filter(ful => ful.shipping_code))
      .catch((e) => {
        debug.error("[FFM Service:loadFulfillments]", e);
        return [];
      });
  }

  loadFulfillmentsByShop(shopToken, paging: any = { limit: 10000 }) {
    let headers = this.http.createHeader(shopToken);
    let options = this.http.createDefaultOption(headers);
    return this.http.postWithOptions("api/shop.Fulfillment/GetFulfillments", { paging }, options).toPromise()
      .then(res => res.fulfillments.filter(ful => ful.shipping_code));
  }

  loadFulfillment(id: string, token?: string) {
    let headers = this.http.createHeader(token);
    let options = this.http.createDefaultOption(headers);
    return this.http.postWithOptions("api/shop.Fulfillment/GetFulfillment", {
      id
    }, options).toPromise()
      .then(res => this.fulfillmentMap(res));
  }

  async getFulfillments(shop_id?: string, start?: number, perpage?: number, pFilters?: Array<any>) {
    let mixed = {};
    let paging = {
      offset: start || 0,
      limit: perpage || 20
    };
    let filters = [];

    let shop = this.auth.accounts.find(sa => sa.id == shop_id);
    let token = shop ? shop.token : null;

    if (shop) {
      mixed['ids'] = [shop.id];
    } else {
      mixed['all'] = true;
    }

    if (pFilters instanceof Array) {
      filters = pFilters.map(f => new Object({
        name: f.name_rule == "date" ? "created_at" : f.name_rule,
        op: this.util.filterOperatorMap(f.compair_rule, f.name_rule == "date"),
        value: f.value
      }));
    }

    const options = this.getOptions(token);
    return this.http.postWithOptions('api/shop.Fulfillment/GetFulfillments', {
      paging,
      filters,
      mixed
    }, options).toPromise().then(res => {
        let fulfillments = res.fulfillments.map(o => this.fulfillmentMap(o));
        return {
          paging: res.paging,
          fulfillments: fulfillments
        };
      });
  }

  getShipmentTypeFromCode(shippingCode) {
    return shippingCode.split("-")[0].toLowerCase();
  }

  async checkEmptyFulfillments(shop_id) {
    const token = this.auth.getTokenByShop(shop_id);

    const ffms = await this.loadFulfillmentsByShop(token, { limit: 1, offset: 0 });
    return ffms.length > 0;
  }

  canForcePicking(fulfillment: Fulfillment) {
    const { status_mapped } = fulfillment;
    if (!status_mapped) {
      return false;
    }
    const { state } = status_mapped;
    return state === 'picking' || state === 'created' || state === 'confirmed';
  }

  canForceDelivering(fulfillment: Fulfillment) {
    const { status_mapped } = fulfillment;
    if (!status_mapped) {
      return false;
    }
    const { state } = status_mapped;
    return state === 'delivering' || state === 'holding';
  }

  checkStatusForceItem(shipping_code: string) {
    const item = this.forceList.get(shipping_code);
    if (!item) {
      return false;
    }
    const now = moment.now();
    return item.expireTime > now;
  }

  getLocalForceList() {
    const data: any = localStorage.getItem(this.__secretCode);
    const list = data ? JSON.parse(data).forceList : [];
    const now = moment.now();
    this.forceList = new List(list.filter(i => i.expireTime > now));
  }

  addForceItem(shipping_code, type) {
    const expireTime = moment(moment.now()).add(6, 'hours').valueOf();
    this.forceList.add({
      id: shipping_code,
      type,
      expireTime
    });
    this.updateLocalForceList();
  }

  updateLocalForceList() {
    const data = { forceList: this.forceList.asArray( )};
    localStorage.setItem(this.__secretCode, JSON.stringify(data));
  }

  async getFulfillmentHistory(fulfillment: Fulfillment) {
    const ffmId = fulfillment.id;
    const token = this.auth.getTokenByShop(fulfillment.shop_id);
    let ret = [];
    try {
      const res = await this.http.customPost(
        "api/shop.History/GetFulfillmentHistory",
        { paging: { limit: 100 }, id: ffmId },
        token).toPromise();
      const history = res.data.map(h => {
        const time = new Date(h.last_sync_at || h._time).getTime();
        return { ...h, time };
      }).sort((h1, h2) => h1.time - h2.time);

      for (let h of history) {
        const date = h.last_sync_at || h._time;
        let event = "";
        const currentStatus = h.shipping_state;

        switch (currentStatus) {
          case "cancelled":
            event = "Hủy đơn";
            break;
          case "closed":
            event = "Hoàn tất";
            break;
          case "undeliuverable":
            event = "Không giao được";
            break;
          case "returned":
            event = "Đã trả hàng";
            break;
          case "returning":
            event = "Đang trả hàng";
            break;
          case "delivered":
            event = "Đã giao hàng";
            break;
          case "delivering":
            event = "Đang giao hàng";
            break;
          case "holding":
            event = "Chờ giao";
            break;
          case "picking":
            event = "Đang lấy hàng";
            break;
          case "created":
            event = "Tạo đơn";
            break;
          default:
            break;
        }

        if (event) {
          let isDuplicate = false;
          for (let r of ret) {
            if (r.date.substr(0, 16) == date.substr(0, 16) && r.event == event) {
              isDuplicate = true;
              break;
            }
          }

          if (isDuplicate) {
            continue;
          }
          ret.push({ date, event });
        }
      }
    } catch (e) {
      debug.error(e);
    }

    return ret;
  }

  providerLogo(fulfillment) {
    try {
      const { shipping_provider } = fulfillment;
      return providerLogo[shipping_provider];
    } catch (e) {
      return providerLogo.default;
    }
  }

  providerLogoSm(fulfillment) {
    try {
      const { shipping_provider } = fulfillment;
      return providerLogoSm[shipping_provider];
    } catch (e) {
      return providerLogoSm.default;
    }
  }

  trackingLink(fulfillment) {
    if (!fulfillment) {
      return null;
    }
    switch (fulfillment.shipping_provider) {
      case 'ghn':
        return `https://track.ghn.vn/order/tracking?code=${fulfillment.shipping_code}`;
      case 'vtpost':
        return `https://viettelpost.com.vn/Tracking?KEY=${fulfillment.shipping_code}`;
      default:
        return null;
    }
  }

  getOptions(token) {
    const headers = this.http.createHeader(token);
    const options = this.http.createDefaultOption(headers);
    return options;
  }
}



