import {District, Province, Ward} from "./location";
import {AModel} from "./AModel";

export class Address extends AModel {
  province: string;
  province_code: string;
  district: string;
  district_code: string;
  ward: string;
  ward_code: string;
  address1: string;
  address2: string;
  zip: string;
  country: string;
  full_name: string;
  first_name: string;
  last_name: string;
  phone: string;
  email: string;
  position: string;
  type: any;
  notes: any;

  o_data: any;
  onSubmit: (Address) => void;

  Province: Province;
  District: District;
  Ward: Ward;

  constructor(obj) {
    super(obj);
    if(!this.notes) {
      this.notes = {};
    }
  }
}

export class OrderAddress extends AModel {
  full_name: string;
  first_name: string;
  last_name: string;
  phone: string;
  country: string;
  city: string;
  province: string;
  province_code: string;
  district: string;
  district_code: string;
  ward: string;
  ward_code: string;
  zip: string;
  company: string;
  address: string;
  address1: string;
  address2: string;

  o_data: any;

  notes: any;

  Province: Province;
  District: District;
  Ward: Ward;
}

export class ShopShippingAddress {
  sh_address: OrderAddress;
  x_service_id: string;
  x_shipping_fee: number;
  x_service_name: string;
  shipping_provider: string;
  include_insurance: boolean;
}



