import { NgModule } from "@angular/core";
import { CreateRangePipe } from "./create-range.pipe";
import { DotReplacePipe } from "./dot-replace.pipe";
import { StatusOrder, StatusVN, VND, StatusPipe } from "./etop.pipe";
import { SafeHtmlPipe, SafeUrlPipe } from "./safe-html.pipe";
import { KeysPipe } from "./keys.pipe";

const pipes = [
  CreateRangePipe,
  DotReplacePipe,
  StatusOrder,
  StatusVN,
  StatusPipe,
  VND,
  SafeHtmlPipe,
  SafeUrlPipe,
  KeysPipe
];

@NgModule({
  declarations: pipes,
  exports: pipes
})
export class SharedPipeModule {}



