import {Injectable} from '@angular/core';
import {HttpService} from "./http.service";
import {AuthenticateService} from "./authenticate.service";
import {StorageService} from "./storage.service";
import {Address} from "./models/address";
import List from "identical-list";
import {Subject} from 'rxjs/Subject';

@Injectable()
export class AddressService {

  __service_key = "address";
  addressList = new List<Address>();

  token = "";
  loading = false;

  private addressChange = new Subject();

  constructor(
    private storage: StorageService,
    private auth: AuthenticateService,
    private http: HttpService) {
    this._loadData();
    this.storage.onLocalStorageUpdated.subscribe(() => {
      this._loadData();
    });
    this._init();
    this.auth
      .authenInfo
      .distinctUntilKeyChanged('token')
      .subscribe(({token}) => {
        if (this.token != token) {
          this._init();
        }
        this.token = token;
      });
  }


  get change() {
    return this.addressChange.asObservable();
  }


  private _loadData() {
    let data = this.storage.get(this.__service_key);
    if (!data) {
      return;
    }

    let {addresses} = data;
    this.addressList = new List<Address>(addresses);
  }

  private async _init() {
    try {
      this.loading = true;
      let addresses = await this.loadAddresses();
      this.loading = false;
      this.addressList = new List<Address>(addresses);
      this.storage.set(this.__service_key, {
        addresses
      });
      this.addressChange.next(this.addressList.asArray());
    } catch (e) {
      debug.error(e);
    }
  }

  createAddress(body, token?) {
    let headers = this.http.createHeader(token);
    let options = this.http.createDefaultOption(headers);
    return this.http.postWithOptions('/api/etop.Address/CreateAddress', body, options).toPromise()
      .then(res => {
        this.addressList.add(res);
        return res;
      });
  }

  updateAddress(body) {
    return this.http.post('/api/etop.Address/UpdateAddress', body).toPromise()
      .then(res => {
        this.addressList.add(res);
        return res;
      });
  }

  getAddresses() {
    if (this.addressList.length) {
      return this.addressList.asArray();
    } else {
      return this.http.post('/api/etop.Address/GetAddresses', {}).toPromise()
        .then(res => res.addresses);
    }
  }

  loadAddresses() {
    return this.http.post('/api/etop.Address/GetAddresses', {}).toPromise()
      .then(res => res.addresses);
  }

  removeAddress(id) {
    return this.http.post('/api/etop.Address/RemoveAddress', {'id': id}).toPromise()
      .then(() => this.addressList.deleteId(id));
  }

  joinAddress(address) {
    let res = '';
    if (address && address.address) {
      res += address.address + ", ";
    } else if (address && address.address1) {
      res += address.address1 + ", ";
    }

    if (address && address.ward) {
      res += address.ward + ", ";
    }

    if (address && address.district) {
      res += address.district + ", ";
    }

    if (address && address.province) {
      res += address.province;
    }

    return res;
  }
}



