import {AModel} from "./AModel";

export class Product extends AModel {
  id: string;
  name: string;
  description: string;
  short_desc: string;
  desc_html: string;
  quantity_available: number;
  status: "default" | "active" | "disabled";
  status_display: string;
  image: string;
  image_urls: Array<string>;
  meta: any;

  price: number;
  wholesale_price: number;
  cost_price: number;
  list_price_min: number;
  list_price_max: number;

  brand_id: string;
  collection_id: string;
  category_id: string;
  category: any;
  brand_info: any;

  variants: Array<Variant>;
  haravan_id: string;

  o_data: any;
  p_data = {
    selected: false,
    shift: false,
    collections: {
      name: ''
    },
    category: {},
    haravan_error: null
  };

  attributes: any;

  product_source_id: string;
  product_source_name: string;
  product_source_type: string;
  collections: string;
  collection_ids: Array<string>;
  code: string;
}

export class ShopProduct extends Product {
  tags: Array<string>;
  updated_at: string;
}

export class Variant extends AModel {
  name: string;
  list_price: number;
  retail_price: number;
  wholesale_price: number;
  cost_price: number;
  list_price_min: number;
  list_price_max: number;
  quantity_available: number;
  status: string;
  o_data: any;

  image_urls: Array<string>;
  image_url: string;
  attributes: any;
  sku: string;
  product_source_type: string;
}



