import {Injectable} from '@angular/core';
import {HttpService} from "./http.service";
import {AuthenticateService} from "./authenticate.service";
import List from "identical-list";
import {Subject} from "rxjs/Subject";
import { TimerService } from './timer.service';

@Injectable()
export class SyncService {

  queueHash = {};
  errorLogHash = {};

  onLogUpdated = new Subject();

  private _account_id;
  private _last_account_id;

  constructor(private http: HttpService,
              private auth: AuthenticateService,
              private timerService: TimerService) {
    this._account_id = this.auth.account && this.auth.account.id;
    // this.init();
    this.auth.authenInfo.subscribe(info => {
      this._account_id = info.account && info.account.id;
    });
  }

  private async init() {
    let refreshProduct = async () => {
      await this._loadHaravanLog();
      this.timerService.setTimeout(refreshProduct, 30000, 'SYNC_HARAVAN', true);
    };
    refreshProduct();
  }

  private _invokeUpdate() {
    this.onLogUpdated.next({
      queueHash: this.queueHash,
      errorLogHash: this.errorLogHash
    });
  }

  private async _loadHaravanLog() {
    if (!this._account_id) {
      return;
    }
    try {
      let res: any = await this.http.get("api/v1/haravan", {
        user_id: this._account_id
      }).toPromise();
      if (res.success) {
        let {queue, error} = res.data;
        this.queueHash = {};
        this.errorLogHash = {};
        queue.forEach(q => {
          q.data.product_ids.forEach(p => {
            this.queueHash[p] = q;
          });
        });
        error.forEach(e => {
          this.errorLogHash[e.target_id] = e;
        });
        this._invokeUpdate();
      }
    } catch (e) {
      debug.error("haravan error", e);
    }
    this._last_account_id = this._account_id;
  }

  async publishProduct(ids: Array<Array<string>>) {
    let res = await this.http.post("api/v1/haravan", [{
      groups: ids,
      access_token: this.auth.token,
      account: this.auth.account
    }]).toPromise();
    ids.forEach((id: any) => this.queueHash[id] = true);
    this._invokeUpdate();
    return res;
  }

  deleteProduct(ids, haravan_id) {
    return this.http.post("api/v1/haravan", [{
      product_ids: ids,
      haravan_id: haravan_id,
      access_token: this.auth.token,
      account: this.auth.account,
      action: 'delete'
    }]).toPromise();
  }

  publishCollection(collection) {
    return this.http.post("api/v1/haravan-collection", {
      collection: collection,
      account: this.auth.account,
      action: 'publish'
    }).toPromise();
  }

  deleteCollection(collection) {
    return this.http.post("api/v1/haravan-collection", {
      collection: collection,
      account: this.auth.account,
      action: 'delete'
    }).toPromise();
  }
}



