import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'dotReplace'
})
export class DotReplacePipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if (value) {
      return value.replace(/,/g, ".");
    }

    return '';
  }

}



