import {Injectable} from '@angular/core';
import {Title} from "@angular/platform-browser";
import {environment} from "../../environments/environment";
import {AuthenticateService} from "./authenticate.service";

@Injectable()
export class TitleService {

  private _prefix;

  constructor(private title: Title, private auth: AuthenticateService) {
    this._prefix = environment.title_prefix || "EYE Solution Portal";
  }

  setTitle(title?: string) {
    this.title.setTitle(this._prefix + (this.auth.shop && this.auth.shop.name ? ` - ${this.auth.shop.name}` : "" ) +(title ? ` - ${title}` : ""));
  }
}



