import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from '../../environments/environment';
import { AuthenticateService } from './authenticate.service';
import { FulfillmentService } from './fulfillment.service';
import { Order } from './models/order';
import * as moment from 'moment';
import { Fulfillment } from './models/fulfillment';
@Injectable()
export class TelegramService {
  groupProdIds = {
    confirm: '-278520131',
    cancel: '-239024220',
    newShop: '-298762499',
    newAccount: '-270142863',
    force: {
      ghn: '-268371477',
      ghtk: '-222154225',
      vtpost: '-238517539'
    }
  };

  groupId = '-285363565';
  bugReportGroupId = '-185962908';

  constructor(private http: HttpClient,
    private fulfillmentService: FulfillmentService,
    private auth: AuthenticateService) {
     }

  sendMessage(fastBuyGroupId, msg, enableHTML = true) {
    msg += '\n\nSource: ' + window.location.hostname;

    const fastBuyUrl = 'https://api.telegram.org/bot156097778:AAGUkHpuAY-RihvCVHA7g_E1vHfEnb45pF4/sendMessage';
    const fastbuyData: any = {
      'chat_id': fastBuyGroupId,
      'text': msg,
    };
    if (enableHTML) {
      fastbuyData.parse_mode = 'HTML';
    }

    return this.http.post(fastBuyUrl, fastbuyData).toPromise();
  }

  async sendOrderTelegramMessage(order, action, fromAddress, toAddress, reason?) {
    var fastBuyGroupId = '';
    if (this.isProduction()) {
      fastBuyGroupId = action == 'confirm' ? this.groupProdIds.confirm : this.groupProdIds.cancel;
    } else {
      fastBuyGroupId = this.groupId;
    }

    let reason_line = action == 'confirm' ? '' : `<strong>Lý do: </strong>${reason}\n`;

    const fulfillment = order.fulfillments && order.fulfillments[0];
    const quantity = order.total_items || (fulfillment && fulfillment.total_items) || 'Không xác định';
    const shipping_code = fulfillment && fulfillment.shipping_code;
    const shipping_fee_shop = fulfillment ? fulfillment.shipping_fee_shop : null;
    const { shipping_provider, include_insurance, x_service_name } = order.shop_shipping;

    let msg = `
<a href="https://admin.etop.vn/admin/orders/${order.id}">#${order.code}</a>: SHOP ĐÃ ${action == 'confirm' ? 'XÁC NHẬN' : 'HỦY'}!

Shop <strong>${order.shop_name}</strong>
${reason_line}
${fulfillment ? `<strong>${
  shipping_provider.toUpperCase()
}</strong> <a href="#${this.fulfillmentService.trackingLink(fulfillment) || ''}">#${shipping_code}</a> ${
  action === 'confirm' ? ` - ${x_service_name} - <strong>${this.moneyFormat(shipping_fee_shop)}</strong>`  : ''}` : ''}

☻ Gửi: <strong>${fromAddress}</strong>
☻ Nhận: <strong>${toAddress}</strong>

Khách hàng: <strong>${order.customer_address.full_name || order.customer.full_name}</strong>
Số lượng: <strong>${quantity}</strong> sản phẩm

Bảo hiểm: <strong>${include_insurance ? 'Có' : 'Không'}</strong>
Tạm tính: <strong>${this.moneyFormat(order.basket_value)}</strong>
Phí giao hàng: <strong>${this.moneyFormat(order.shop_shipping_fee || 0)}</strong>
Giảm giá: <strong>${this.moneyFormat(order.total_discount || 0)}</strong>

TỔNG TIỀN: <strong>${this.moneyFormat(order.total_amount)}</strong>
COD: <strong>${this.moneyFormat(order.shop_cod || 0)}</strong>
`;

    try {
      await this.sendMessage(fastBuyGroupId, msg);
      debug.log("SENT MESSAGE");
    } catch (e) {
      debug.log("ERROR SEND MESSAGE");
    }
  }

  async sendConfirmedMultiOrderMessage(orderCount: number, shopName) {
    let fastBuyGroupId = '';
    if (this.isProduction()) {
      fastBuyGroupId = this.groupProdIds.confirm;
    } else {
      fastBuyGroupId = this.groupId;
    }
    let msg = `
SHOP ĐÃ XÁC NHẬN ${orderCount} ĐƠN HÀNG
<strong>Shop ${shopName}</strong>`;

    try {
      await this.sendMessage(fastBuyGroupId, msg);
      debug.log("SENT MESSAGE");
    } catch (e) {
      debug.log("ERROR SEND MESSAGE");
    }
  }

  async sendCancelMultiOrderMessage(orderCount: number, shopName) {
    let fastBuyGroupId = '';
    if (this.isProduction()) {
      fastBuyGroupId = this.groupProdIds.cancel;
    } else {
      fastBuyGroupId = this.groupId;
    }
    let msg = `
SHOP ĐÃ HỦY ${orderCount} ĐƠN HÀNG
<strong>Shop ${shopName}</strong>`;

    try {
      await this.sendMessage(fastBuyGroupId, msg);
      debug.log("SENT MESSAGE");
    } catch (e) {
      debug.log("ERROR SEND MESSAGE");
    }
  }

  moneyFormat(money) {
    if (money === null || money === undefined) {
      return '--';
    }
    return money.toString().replace(/(\d)(?=(\d\d\d)+(?!\d))/g, "$1,") + "đ";
  }

  async newMerchantMessage(account) {
    let fastBuyGroupId = this.isProduction() ? this.groupProdIds.newAccount : this.groupId;
    let msg = `
<strong>ĐĂNG KÝ TÀI KHOẢN!</strong>

Họ tên: <strong>${account.full_name}</strong>
Số điện thoại: ${account.phone}
Email: ${account.email}`;

    try {
      await this.sendMessage(fastBuyGroupId, msg);
      debug.log("SENT MESSAGE");
    } catch (e) {
      debug.log("ERROR SEND MESSAGE");
    }
  }

  async forceMessage(type: string, shopName: string, message: string, order: Order, fulfillment?: Fulfillment) {
    let ffm;
    if (!fulfillment) {
      ffm = order.fulfillments && order.fulfillments[0];
    } else {
      ffm = fulfillment;
    }
    const { shop_shipping, customer_address } = order;
    const { x_service_id } = shop_shipping;
    const { shipping_provider, shipping_code, expected_delivery_at, expected_pick_at } = ffm;
    let account = 'unknown';
    let login = '';
    switch (shipping_provider) {
      case "ghn":
        if (x_service_id.charAt(0) === 'D') {
          account = 'TK 300 gr';
          login = '0938222489';
        } else if (x_service_id.charAt(0) === 'E') {
          account = 'TK 500 gr';
          login = '0976358769';
        }
        break;
      case "ghtk":
        if (x_service_id.charAt(1) === 'D') {
          account = 'TK Default';
          login = 'giang2224@gmail.com';
        } else if (x_service_id.charAt(2) === 'S') {
          account = 'TK Đồng giá';
          login = 'giang@etop.vn';
        }
        break;
      default:
        account = 'vtpost';
    }

    let title = 'GIAO';
    let expected = moment(expected_delivery_at).format('HH:mm DD/MM');
    let diff = moment(moment.now()).diff(moment(expected_delivery_at), 'hours');

    if (type === 'pick') {
      title = 'LẤY';
      expected = `trước ${moment(expected_pick_at).format('HH:mm DD/MM')}`;
      diff = moment(moment.now()).diff(moment(expected_pick_at), 'hours');
    }
    const delay = moment.duration(diff, 'hours').humanize();
    const fastBuyGroupId = this.isProduction() ? this.groupProdIds.force[shipping_provider] : this.groupId;
    const msg = `
<strong>${shipping_code}</strong> - GIỤC ${title}
--
Lý do: ${message}
--
${shipping_provider.toUpperCase()} ${account}
${login ? `Login <strong>${login}</strong>` : ''}
--
KH: ${customer_address.full_name}
SĐT: ${customer_address.phone}
Dự kiến ${title.toLowerCase()}: <strong>${expected}</strong>
${diff > 0 ? `Trễ: <strong>${delay}</strong>` : 'Chưa trễ'}

Shop: <strong>${shopName}</strong>
    `;
    try {
      await this.sendMessage(fastBuyGroupId, msg);
      this.fulfillmentService.addForceItem(shipping_code, type);
    } catch (e) {
      debug.error("ERROR SEND MESSAGE", e);
    }
  }

  async newShopMessage(user, shop, address) {
    let fastBuyGroupId = this.isProduction() ? this.groupProdIds.newShop : this.groupId;
    let msg = `
<strong>ĐĂNG KÝ CỬA HÀNG!</strong>

Tên cửa hàng: <strong>${shop.name}</strong>
Số điện thoại: ${shop.phone}
Website: ${shop.website_url}
Email: ${shop.email}
Địa chỉ: ${address}
—————————
Thuộc tài khoản: <strong>${user.full_name}</strong>
Số điện thoại: ${user.phone}
Email: ${user.email}
    `;
    try {
      await this.sendMessage(fastBuyGroupId, msg);
      debug.log("SENT MESSAGE");
    } catch (e) {
      debug.log("ERROR SEND MESSAGE");
    }
  }

  async updateShopMessage(user, shop, address) {
    const fastBuyGroupId = this.isProduction() ? this.groupProdIds.newShop : this.groupId;
    const msg = `
<strong>CẬP NHẬT THÔNG TIN CỬA HÀNG!</strong>

Tên cửa hàng: <strong>${shop.name}</strong>
Số điện thoại: ${shop.phone}
Website: ${shop.website_url}
Email: ${shop.email}
Địa chỉ: ${address}
—————————
Thuộc tài khoản: <strong>${user.full_name}</strong>
Số điện thoại: ${user.phone}
Email: ${user.email}
    `;
    try {
      await this.sendMessage(fastBuyGroupId, msg);
      debug.log("SENT MESSAGE");
    } catch (e) {
      debug.log("ERROR SEND MESSAGE");
    }
  }

  bugReport(content, images) {
    if (images.length) {
      return Promise.all(images.map((image, index) => {

        let msg = index == 0 ? `
Lỗi: <strong>${content}</strong>

Hình ảnh mô tả:
<a href="${image}">⁠</a>
          ` : `
<a href="${image}">⁠</a>
            `;

        return this.sendMessage(this.bugReportGroupId, msg);
      }));

    } else {
      let msg = `
Lỗi: <strong>${content}</strong>`;
      return this.sendMessage(this.bugReportGroupId, msg);
    }
  }

  bugTracking(api, body, response) {
    const msg = `
API: ${api}
Body: ${JSON.stringify(body, undefined, 2)}
Response: ${JSON.stringify(response, undefined, 2)}`;
    debug.log(msg);
    this.sendMessage(this.groupId, msg, false);
  }

  isProduction() {
    if (this.auth.shop && this.auth.shop.is_test) {
      return false;
    }
    if (environment.production && window.location.href.indexOf('d.etop.vn') < 0) {
      return true;
    }

    return false;
  }
}



