import * as _ from "lodash";
import {Injectable} from '@angular/core';
import { HttpService } from "./http.service";


declare var unescape;

@Injectable()
export class UtilService {
  note: string;

  constructor(private httpService: HttpService) {
    this.getNote().then(res => this.note = res);
  }

  createFetchLoop(func, cycle = 30000) {
    let refreshProduct = async () => {
      await func();
    };
    refreshProduct();
    let interval = setInterval(func, cycle);
    return interval;
  }

  groupProduct(products: [any]) {
    let groupHash = {};
    products.sort((a, b) => parseInt(b.id, 10) - parseInt(a.id, 10));
    products.filter(p => p.info && !p.info.x_base_id)
      .concat(products.filter(p => !p.x_base_id && !p.info))
      .forEach(p => {
        let id = p.x_id || (p.info && p.info.x_id);
        groupHash[id] = Object.assign(p, {
          variants: []
        });
      });
    products.filter(p => p.x_base_id || (p.info && p.info.x_base_id)).forEach(p => {
      let base_id = p.x_base_id || (p.info && p.info.x_base_id);
      let product = groupHash[base_id];
      if (product) {
        if (!product.variant_keys) {
          product.variant_keys = [];
        }
        let attrValue = p.attributes.map(a => a.value).join(",");
        if (product.variant_keys.indexOf(attrValue) > -1) {
          return;
        }
        product.variant_keys.push(attrValue);
        p.image_urls = p.image_urls.length ? p.image_urls : groupHash[base_id].image_urls.slice(0, 1);
        groupHash[base_id].variants.push(p);
      }
    });
    return <[any]>Object.values(groupHash);
  }

  removeDiacritic(str) {
    str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
    str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
    str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
    str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
    str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
    str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
    str = str.replace(/đ/g, "d");
    str = str.replace(/À|Á|Ạ|Ả|Ã|Â|Ầ|Ấ|Ậ|Ẩ|Ẫ|Ă|Ằ|Ắ|Ặ|Ẳ|Ẵ/g, "A");
    str = str.replace(/È|É|Ẹ|Ẻ|Ẽ|Ê|Ề|Ế|Ệ|Ể|Ễ/g, "E");
    str = str.replace(/Ì|Í|Ị|Ỉ|Ĩ/g, "I");
    str = str.replace(/Ò|Ó|Ọ|Ỏ|Õ|Ô|Ồ|Ố|Ộ|Ổ|Ỗ|Ơ|Ờ|Ớ|Ợ|Ở|Ỡ/g, "O");
    str = str.replace(/Ù|Ú|Ụ|Ủ|Ũ|Ư|Ừ|Ứ|Ự|Ử|Ữ/g, "U");
    str = str.replace(/Ỳ|Ý|Ỵ|Ỷ|Ỹ/g, "Y");
    str = str.replace(/Đ/g, "D");
    return str;
  }

  createHandle(str) {
    if (str) {
      str = str.trim();
      str = str.toLowerCase();
      str = str.replace(/à|á|ạ|ả|ã|â|ầ|ấ|ậ|ẩ|ẫ|ă|ằ|ắ|ặ|ẳ|ẵ/g, "a");
      str = str.replace(/è|é|ẹ|ẻ|ẽ|ê|ề|ế|ệ|ể|ễ/g, "e");
      str = str.replace(/ì|í|ị|ỉ|ĩ/g, "i");
      str = str.replace(/ò|ó|ọ|ỏ|õ|ô|ồ|ố|ộ|ổ|ỗ|ơ|ờ|ớ|ợ|ở|ỡ/g, "o");
      str = str.replace(/ù|ú|ụ|ủ|ũ|ư|ừ|ứ|ự|ử|ữ/g, "u");
      str = str.replace(/ỳ|ý|ỵ|ỷ|ỹ/g, "y");
      str = str.replace(/đ/g, "d");
      str = str.replace(/\,/g, '-');
      str = str.replace(/\_/g, '-');
      str = str.replace(/\./g, '-');
      str = str.replace(/\!/g, '-');
      str = str.replace(/\?/g, '-');
      str = str.replace(/\~/g, '-');
      str = str.replace(/\ /g, '-');
      str = str.replace(/\|/g, '-');
      str = str.replace(/\./g, '-');
      str = str.replace(/\"/g, '-');
      str = str.replace(/\'/g, '-');
      str = str.replace(/\-\-+/g, '-');
      str = str.replace(/\s+/g, '-');
      str = str.replace(/[^\w\-]+/g, '');
      str = str.replace(/\-\-+/g, '-');
      str = str.replace(/^-+/, '');
      str = str.replace(/-+$/, '');
      if (str.slice(-1) == '-') {
        str = str.substring(0, str.length - 1);
      }
    }
    return str;
  }

  searchCompare(_name, _keyword, operator: string = '&=') {
    if (_keyword === null || _keyword === undefined) {
      return true;
    }
    if (_name === null || _name === undefined) {
      return false;
    }
    const name = this.removeDiacritic(_name.toLowerCase()).trim();
    const keyword = this.removeDiacritic(_keyword.toLowerCase().replace(/\s+/g, ' ')).trim();
    if (operator == '==') {
      return name == keyword;
    }
    let words = keyword.split(' ');
    if (operator == '&=') {
      return words.reduce((l, r) => l && name.indexOf(r) > -1, true);
    }
    if (operator == '!=') {
      return !(words.reduce((l, r) => l && name.indexOf(r) > -1, true));
    }
  }

  dataURLtoFile(dataurl, filename): File {
    var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], filename, {type: mime});
  }

  fillImage(img, width, height) {
    const canvas = document.createElement('canvas');
    canvas.width = width;
    canvas.height = height;
    const ctx = canvas.getContext('2d');
    ctx.drawImage(img, width / 2 - img.width / 2, height / 2 - img.height / 2);
    return canvas.toDataURL();
  }

  downScaleImage(img, scale, type) {
    const  imgCV = document.createElement('canvas');
    const { width, height } = img;
    imgCV.width = width;
    imgCV.height = height;
    const imgCtx = imgCV.getContext('2d');
    imgCtx.fillStyle = "#fff";
    imgCtx.fillRect(0, 0, width, height);
    imgCtx.drawImage(img, 0, 0);
    return this.downScaleCanvas(imgCV, scale, 'image/jpeg');
  }

  downScaleCanvas(cv, scale, type) {
    if (!(scale < 1) || !(scale > 0)) throw ('scale must be a positive number <1 ');
    var sqScale = scale * scale; // square scale = area of source pixel within target
    var sw = cv.width; // source image width
    var sh = cv.height; // source image height
    var tw = Math.floor(sw * scale); // target image width
    var th = Math.floor(sh * scale); // target image height
    var sx = 0, sy = 0, sIndex = 0; // source x,y, index within source array
    var tx = 0, ty = 0, yIndex = 0, tIndex = 0; // target x,y, x,y index within target array
    var tX = 0, tY = 0; // rounded tx, ty
    var w = 0, nw = 0, wx = 0, nwx = 0, wy = 0, nwy = 0; // weight / next weight x / y
    // weight is weight of current source point within target.
    // next weight is weight of current source point within next target's point.
    var crossX = false; // does scaled px cross its current px right border ?
    var crossY = false; // does scaled px cross its current px bottom border ?
    var sBuffer = cv.getContext('2d').getImageData(0, 0, sw, sh).data; // source buffer 8 bit rgba
    var tBuffer = new Float32Array(4 * tw * th); // target buffer Float32 rgb
    var sR = 0, sG = 0, sB = 0; // source's current point r,g,b
    var sA = 0;  //source alpha  */

    for (sy = 0; sy < sh; sy++) {
      ty = sy * scale; // y src position within target
      tY = 0 | ty;     // rounded : target pixel's y
      yIndex = 4 * tY * tw;  // line index within target array
      crossY = (tY != (0 | ty + scale));
      if (crossY) { // if pixel is crossing botton target pixel
        wy = (tY + 1 - ty); // weight of point within target pixel
        nwy = (ty + scale - tY - 1); // ... within y+1 target pixel
      }
      for (sx = 0; sx < sw; sx++ , sIndex += 4) {
        tx = sx * scale; // x src position within target
        tX = 0 | tx;    // rounded : target pixel's x
        tIndex = yIndex + tX * 4; // target pixel index within target array
        crossX = (tX != (0 | tx + scale));
        if (crossX) { // if pixel is crossing target pixel's right
          wx = (tX + 1 - tx); // weight of point within target pixel
          nwx = (tx + scale - tX - 1); // ... within x+1 target pixel
        }
        sR = sBuffer[sIndex];   // retrieving r,g,b for curr src px.
        sG = sBuffer[sIndex + 1];
        sB = sBuffer[sIndex + 2];

        // !! untested : handling alpha !!
        sA = sBuffer[sIndex + 3];
        // if (!sA) continue;
        // if (sA != 0xFF) {
        //    sR = (sR * sA) >> 8;  // or use /256 instead ??
        //    sG = (sG * sA) >> 8;
        //    sB = (sB * sA) >> 8;
        // }
        if (!crossX && !crossY) { // pixel does not cross
          // just add components weighted by squared scale.
          tBuffer[tIndex] += sR * sqScale;
          tBuffer[tIndex + 1] += sG * sqScale;
          tBuffer[tIndex + 2] += sB * sqScale;

          tBuffer[tIndex + 3] += sA * sqScale;

        } else if (crossX && !crossY) { // cross on X only
          w = wx * scale;
          // add weighted component for current px
          tBuffer[tIndex] += sR * w;
          tBuffer[tIndex + 1] += sG * w;
          tBuffer[tIndex + 2] += sB * w;

          tBuffer[tIndex + 3] += sA * w;

          // add weighted component for next (tX+1) px
          nw = nwx * scale;
          tBuffer[tIndex + 4] += sR * nw;
          tBuffer[tIndex + 5] += sG * nw;
          tBuffer[tIndex + 6] += sB * nw;

          tBuffer[tIndex + 7] += sA * nw;

        } else if (crossY && !crossX) { // cross on Y only
          w = wy * scale;
          // add weighted component for current px
          tBuffer[tIndex] += sR * w;
          tBuffer[tIndex + 1] += sG * w;
          tBuffer[tIndex + 2] += sB * w;

          tBuffer[tIndex + 3] += sA * w;

          // add weighted component for next (tY+1) px
          nw = nwy * scale;
          tBuffer[tIndex + 4 * tw] += sR * nw;
          tBuffer[tIndex + 4 * tw + 1] += sG * nw;
          tBuffer[tIndex + 4 * tw + 2] += sB * nw;

          tBuffer[tIndex + 4 * tw + 3] += sA * nw;

        } else { // crosses both x and y : four target points involved
          // add weighted component for current px
          w = wx * wy;
          tBuffer[tIndex] += sR * w;
          tBuffer[tIndex + 1] += sG * w;
          tBuffer[tIndex + 2] += sB * w;
          tBuffer[tIndex + 3] += sA * w;

          // for tX + 1; tY px
          nw = nwx * wy;
          tBuffer[tIndex + 4] += sR * nw;
          tBuffer[tIndex + 5] += sG * nw;
          tBuffer[tIndex + 6] += sB * nw;
          tBuffer[tIndex + 7] += sA * nw;

          // for tX ; tY + 1 px
          nw = wx * nwy;
          tBuffer[tIndex + 4 * tw] += sR * nw;
          tBuffer[tIndex + 4 * tw + 1] += sG * nw;
          tBuffer[tIndex + 4 * tw + 2] += sB * nw;
          tBuffer[tIndex + 4 * tw + 3] += sA * nw;

          // for tX + 1 ; tY +1 px
          nw = nwx * nwy;
          tBuffer[tIndex + 4 * tw + 4] += sR * nw;
          tBuffer[tIndex + 4 * tw + 5] += sG * nw;
          tBuffer[tIndex + 4 * tw + 6] += sB * nw;
          tBuffer[tIndex + 4 * tw + 7] += sA * nw;

        }
      } // end for sx
    } // end for sy

    // create result canvas
    var resCV = document.createElement('canvas');
    resCV.width = tw;
    resCV.height = th;
    var resCtx = resCV.getContext('2d');
    var imgRes = resCtx.getImageData(0, 0, tw, th);
    var tByteBuffer = imgRes.data;
    // convert float32 array into a UInt8Clamped Array
    var pxIndex = 0; //
    for (sIndex = 0, tIndex = 0; pxIndex < tw * th; sIndex += 4, tIndex += 4, pxIndex++) {
      tByteBuffer[tIndex] = Math.ceil(tBuffer[sIndex]);
      tByteBuffer[tIndex + 1] = Math.ceil(tBuffer[sIndex + 1]);
      tByteBuffer[tIndex + 2] = Math.ceil(tBuffer[sIndex + 2]);
      // tByteBuffer[tIndex + 3] = 255;
      tByteBuffer[tIndex + 3] = Math.ceil(tBuffer[sIndex + 3]);

    }
    // writing result to canvas.
    resCtx.putImageData(imgRes, 0, 0);
    return resCV.toDataURL(type);
  }

  uniqueArray(arr) {
    let hash = {};
    let result = [];
    arr.forEach(item => hash[item] = 1);
    for (let i in hash) {
      if (hash.hasOwnProperty(i) && hash[i] === 1) {
        result.push(i);
      }
    }
    return result;
  }

  fileToImage(file) {
    return new Promise((resolve, _) => {
      let oFReader = new FileReader();
      oFReader.readAsDataURL(file);
      oFReader.onload = (oFREvent: any) => {
        let image = new Image();
        image.src = oFREvent.target.result;

        image.onload = (e: any) => {
          resolve(image);
        };
      };

    });
  }

  async uploadImages(files, size) {
    const formData = new FormData();
    await Promise.all(files.map(async file => {
      const f: any = await this.fileToImage(file);
      const minDimension = Math.min(f.naturalHeight, f.naturalWidth);
      const scale = size / minDimension;
      if (scale < 1) {
        const base64 = this.downScaleImage(f, scale, file.tye);
        formData.append("files", this.dataURLtoFile(base64, file.name));
      } else if (minDimension < 200) {
        const base64 = this.fillImage(f, 200, 200);
        formData.append("files", this.dataURLtoFile(base64, file.name));
      } else {
        formData.append("files", file);
      }
    }));
    return this.httpService.upload(formData);
  }

  deepClone<TClone>(obj: TClone): TClone | any {
    let objC: any = {};
    for (let key in obj) {
      if (obj.hasOwnProperty(key)) {
        if (typeof obj[key] == "object") {
          objC[key] = this.deepClone<any>(obj[key]);
        }
      }
    }
    return Object.assign({}, obj, objC);
  }

  formatCurrency(number) {
    if (!number) {
      return "0";
    }
    if (number < 0) {
      return '-' + (-number).toFixed()
        .replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
    }
    return number.toFixed()
      .replace(/./g, (c, i, a) => i && c !== "," && ((a.length - i) % 3 === 0) ? '.' + c : c);
  }

  uuid() {
    function s4() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    }

    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
  }

  orderSourceMap(source) {
    return {
      null: 'Default',
      'unknown': 'Unknown',
      'default': 'Default',
      'etop_pos': 'Etop POS',
      'haravan': 'Haravan',
      'etop_pxs': 'Etop POS Extension',
      'self': 'Nhập hàng',
      'import': 'Import'
    }[source];
  }

  orderGhnNoteMap(code) {
    return {
      null: '',
      'CHOTHUHANG': 'Cho thử hàng',
      'CHOXEMHANGKHONGTHU': 'Cho xem hàng không thử',
      'KHONGCHOXEMHANG': 'Không cho xem hàng'
    }[code];
  }

  filterOperatorMap(clientOp, raw = false) {
    let map = {
      "==": "=",
      "&=": "c"
    };
    return raw ? clientOp : (map[clientOp] || clientOp);
  }

  matchKeyArray(obj, spec) {
    for (let key of Object.keys(obj)) {
      if (obj.hasOwnProperty(key)) {
        let specs = obj[key];
        let check = specs.some(s => s == spec);
        if (check) {
          return key;
        }
      }
    }
  }

  trimFields(obj, fields: string[]) {
    fields.forEach(f => {
      obj[f] = obj[f] && obj[f].trim();
    });
    return obj;
  }

  filterSpecialCharacters(str) {
    return str.replace(/[^\u00C0-\u1FFF\u2C00-\uD7FF\w\s]+/g, '');
  }

  async getNote() {
    try {
      const res = await fetch('https://www.etop.vn/api/get-banner').then(r => r.json());
      return res.data[9].title;
    } catch (e) {
      debug.log(e);
      return '';
    }
  }
}



