import {Injectable} from '@angular/core';
import {MoneyTransaction} from "./models/payment";
import List from "identical-list";
import {UtilService} from "./util.service";
import {Subject} from "rxjs/Subject";
import {HttpService} from "./http.service";

@Injectable()
export class MoneyTransactionService {

  onMoneyTransactionListUpdated = new Subject();
  loading = false;
  moneyTransactionList = new List<MoneyTransaction>();

  constructor(
    private http: HttpService,
    private util: UtilService
  ) {
    this.init();
  }

  async init() {
    this.loadMoneyTransaction();
  }

  listMoneyTransactions(start?: number, end?: number, filters?: Array<any>) {
    let subset = this.moneyTransactionList.asArray();
    if (filters instanceof Array) {
      for (let filter of filters) {
        subset = subset.filter(p => {
          switch (filter.name_rule) {
            case "shop_name": {
              let shop = p.fulfillments && p.fulfillments[0] && p.fulfillments[0].shop;
              if (!shop) {
                return false;
              }
              return this.util.searchCompare(shop.name, filter.value, filter.compair_rule);
            }
            case "date": {
              return eval(`"${p.created_at}" ${filter.compair_rule} "${filter.value}"`);
            }
            default: {
              return this.util.searchCompare(p[filter.name_rule], filter.value, filter.compair_rule);
            }
          }
        });
      }
    }

    return subset.slice(start || 0, end || subset.length);
  }

  async loadMoneyTransaction() {
    try {
      this.loading = true;
      let transactions = await this.getMoneyTransactions({
        limit: 9999
      });
      this.moneyTransactionList = new List<any>(transactions.map(t => this.moneyTransactionMap(t)));
      this.onMoneyTransactionListUpdated.next(this.moneyTransactionList);
      this.loading = false;
    } catch (e) {

    }
  }

  private moneyTransactionMap(moneyTransaction) {
    switch (moneyTransaction.status) {
      case 'Z':
        moneyTransaction.status_display = 'Chờ chuyển tiền';
        break;
      case 'P':
        moneyTransaction.status_display = 'Đã chuyển tiền';
        break;
    }

    return moneyTransaction;
  }

  getMoneyTransactionById(id: string) {
    return this.moneyTransactionList.get(id);
  }

  getMoneyTransaction(id: string) {
    return this.http.post("api/shop.MoneyTransaction/GetMoneyTransaction", {id})
      .toPromise();
  }

  getMoneyTransactions(paging, filter?: any) {
    return this.http.post("api/shop.MoneyTransaction/GetMoneyTransactions", {paging, filter})
      .toPromise()
      .then(res => res.money_transactions);
  }
}



