import { Injectable } from '@angular/core';
import { HttpService } from "./http.service";
import { UtilService } from "./util.service";
import List from "identical-list";
import { AuthenticateService } from "./authenticate.service";
import { Subject } from "rxjs/Subject";
import { OrderRule } from "../shared-components/filter/filter.component";
import { Order } from "./models/order";
import { ShopShippingAddress } from "./models/address";
import { FilterOption, initFilterOption } from './models/filter-option';
import misc from '../../libs/misc';
import { FulfillmentService } from './fulfillment.service';
import { Fulfillment } from './models/fulfillment';

export enum ShippingType {
  FAST = 'Nhanh',
  STANDARD = 'Chuẩn'
}

export enum ShippingProvider {
  GHN = 'ghn',
  GHTK = 'ghtk',
  VTPOST = 'vtpost',
}

const mapShippingType = {
  normal: ShippingType.STANDARD,
  fast: ShippingType.FAST
};

@Injectable()
export class OrderService {
  shopAccounts = [];
  orderList = new List();

  private _selectedOrder: any;

  ready = false;
  isLoading = false;

  private __secretCode = "order-data";

  defaultProductWeights = new List<FilterOption>([
    { id: '250', value: '250' },
    { id: '500', value: '500' },
    { id: '1000', value: '1000' },
    { id: '1500', value: '1500' },
    { id: '2000', value: '2000' },
  ]);

  LIMIT_CUSTOM_OPTION = 5;

  defaultShipValues = new List<FilterOption>([{ id: '0', value: '0' }]);

  localProductWeights: List<FilterOption>;
  localShipValues: List<FilterOption>;
  localDiscountValues: List<FilterOption>;
  localDiscountPercents: List<FilterOption>;

  ghnNoteCode = {
    CHOTHUHANG: "Cho thử hàng",
    CHOXEMHANGKHONGTHU: "Cho xem hàng không thử",
    KHONGCHOXEMHANG: "Không cho xem hàng",
    DEFAULT: "default"
  };

  shipProviders = [
    {
      imageSrc: "/assets/images/ship-ghn.png",
      name: "ghn",
      services: ["Chuẩn", "Nhanh"]
    },
    {
      imageSrc: "/assets/images/ship-ghtk.png",
      name: "ghtk",
      services: ["Chuẩn", "Nhanh"]
    },
    {
      imageSrc: "/assets/images/ship-vtpost.png",
      name: "vtpost",
      services: ["Chuẩn", "Nhanh"]
    },
  ];

  shippingNotes = [
    {
      code: "CHOTHUHANG",
      name: "Cho thử hàng"
    },
    {
      code: "CHOXEMHANGKHONGTHU",
      name: "Cho xem hàng không thử"
    },
    {
      code: "KHONGCHOXEMHANG",
      name: "Không cho xem hàng"
    }
  ];

  onOrderListUpdated = new Subject();
  onShopAccountsUpdated = new Subject();
  onReady = new Subject();

  minAcceptWeight = 50;

  validateWeightFn = (s: string) => {
    if (s) {
      const num = Number(s);
      if (Number.isNaN(num) || num < this.minAcceptWeight) {
        return false;
      }
    }

    return true;
  }

  constructor(
    private http: HttpService,
    private auth: AuthenticateService,
    private fulfillmentService: FulfillmentService,
    private util: UtilService,
  ) {
    this.shopAccounts = this.auth.accounts.map((acc) => this.util.deepClone(acc));
    this.auth.authenInfo.subscribe(info => {
      this.shopAccounts = info.accounts && info.accounts.map((acc) => this.util.deepClone(acc)) || [];
    });
    this.init();
  }

  init() {
  }

  getLoadStatus() {
    return this.isLoading;
  }

  getOptions(token) {
    const headers = this.http.createHeader(token);
    const options = this.http.createDefaultOption(headers);
    return options;
  }

  orderMap(order) {
    let o = new Order(Object.assign({}, order));

    o.shop_shipping = order.shop_shipping || new ShopShippingAddress();
    o.payment_status_display = this.mapPaymentStatus(order);
    o.source_display = this.util.orderSourceMap(order.source);
    o.ghn_note_display = this.util.orderGhnNoteMap(order.ghn_note_code);

    o.fulfillments = o.fulfillments.map(f => this.fulfillmentService.fulfillmentMap(f));

    o.status_display = this.mapOrderStatus(o);
    return o;
  }

  async getFulfillmentLogs(order: Order): Promise<Fulfillment[]> {
    if (!order.fulfillments) {
      return [];
    }
    const fulfillments = await Promise.all(order.fulfillments.map(async ffm => {
      const l = await this.fulfillmentService.getFulfillmentHistory(ffm);
      return new Fulfillment({ ...ffm, logs: l });
    }));
    return fulfillments;
  }

  mapOrderStatus(order: Order) {
    const statusMap = {
      "Z": "Mới",
      "S": "Đang xử lý",
      "P": "Thành công",
      "N": "Huỷ",
      "NS": "Trả hàng"
    };
    return statusMap[order.status] || "Không xác định";
  }

  mapPaymentStatus(order) {
    const statusMap = {
      N: "Chưa thanh toán",
      Z: "Không cần thanh toán",
      P: "Đã thanh toán",
      S: "Cần thanh toán"
    };
    return statusMap[order.etop_payment_status];
  }

  async checkEmptyOrders(shop_id) {
    const options = this.getOptions(this.auth.getTokenByShop(shop_id));
    const paging = {
      offset: 0,
      limit: 1
    };
    const res = await this.http.postWithOptions("api/shop.Order/GetOrders", { paging }, options).toPromise();
    const { orders } = res;
    return orders.length > 0;
  }

  getOrders(shop_id?: number, start?: number, perpage?: number, pFilters?: Array<any>, orderRule: OrderRule = {
    name: "Ngày tạo: Mới -> Cũ",
    value: "created_at",
    order: "desc",
    valueMap: (value) => new Date(value).getTime()
  }) {
    let mixed = {};
    let paging = {
      offset: start || 0,
      limit: perpage || 20
    };
    let filters = [];

    let shop = this.shopAccounts.find(sa => sa.id == shop_id);
    let token = shop ? shop.token : null;

    if (!shop) {
      mixed = {
        all: true
      };
    }

    if (shop) {
      mixed['ids'] = [shop.id];
    }

    if (pFilters instanceof Array) {
      filters = pFilters.map(f => new Object({
        name: f.name_rule == "date" ? "created_at" : f.name_rule,
        op: this.util.filterOperatorMap(f.compair_rule, f.name_rule == "date"),
        value: f.value
      }));
    }
    const options = this.getOptions(token);
    return this.http.postWithOptions('api/shop.Order/GetOrders', {
      paging,
      filters,
      mixed
    }, options).toPromise()
      .then(res => {
        return {
          paging: res.paging,
          orders: res.orders.map(o => this.orderMap(o))
        };
      });
  }

  triggerUpdateOrderList() {
    this.onOrderListUpdated.next(this.orderList);
  }

  private async _pullOrder(token, order_id) {
    try {
      let order = await this.getOrder(token, order_id);
      let shop = this.shopAccounts.find(s => s.id == order.shop_id);
      order.token = shop.token;
      order.shop = shop;
      this.orderList.add(order);
      debug.log("_pullOrder", token, order_id, order);
    } catch (e) {
      debug.error(e);
    }
  }

  getOrder(token, id) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/GetOrder", { "id": id }, options).toPromise()
      .then(res => this.orderMap(res));
  }

  getExternalShippingServices(body) {
    return this.http.post("api/shop.Fulfillment/GetExternalShippingServices", body)
      .toPromise().catch(() => ({ services: [] }));
  }

  getPublicExternalShippingServices(body) {
    return this.http.post("api/shop.Fulfillment/GetPublicExternalShippingServices", body)
    .toPromise().catch(() => ({ services: [] }));
  }

  removeSavingPackage(services) {
    return services.filter(s => s.name !== 'Tiết kiệm');
  }

  getOrderById(id, fetchForce = false, token?: string) {
    let subset = this.orderList.asArray();
    token = token || null;
    if (fetchForce || !subset || !subset.length) {
      let headers = this.http.createHeader(token);
      let options = this.http.createDefaultOption(headers);
      return this.http.postWithOptions("api/shop.Order/GetOrder", { "id": id }, options).toPromise().then(o => {
        let shop = this.shopAccounts.find(a => a.id == o.shop_id) || {};
        o.token = shop.token;
        o.shop = shop;
        o = this.orderMap(o);
        return o;
      });
    } else {
      return subset.find(order => order.id == id);
    }
  }

  getOrdersByIds(ids) {
    let subset = this.orderList.asArray();
    return subset.filter(order => ids.indexOf(order.id) > -1);
  }

  updateOrderStatus(token, ids, s_status, cancel_reason?) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/UpdateOrdersStatus", {
      "ids": ids,
      "confirm": "P",
      "cancel_reason": cancel_reason,
      "status": s_status
    }, options).toPromise();
  }

  updateFulfillmentShippingState(token, ids, shipping_state) {
    let headers = this.http.createHeader(token);
    let options = this.http.createDefaultOption(headers);
    return this.http.postWithOptions("api/shop.Fulfillment/UpdateFulfillmentsShippingState", {
      "ids": ids,
      "shipping_state": shipping_state
    }, options).toPromise();
  }

  confirmOrderAndCreateFulfillment(token, order_id) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/ConfirmOrderAndCreateFulfillments", {
      "order_id": order_id
    }, options).toPromise()
      .then(res => {
        if(res.order) {
          this._pullOrder(null, res.order.id);
        }
        return res;
      });
  }

  confirmOrdersAndCreateFulfillments(token, order_ids) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/ConfirmOrdersAndCreateFulfillments", {
      "order_ids": order_ids
    }, options).toPromise();
  }

  createOrder(body, token?) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/CreateOrder", body, options).toPromise()
      .then(res => {
        this._pullOrder(null, res.id);
        return res;
      });
  }

  updateOrder(token, body) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/UpdateOrder", body, options).toPromise()
      .then(res => {
        this._pullOrder(null, res.id);
        return res;
      });
  }

  getShopOrders(token, opt = { paging: { limit: 100000 } }) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/GetOrders", opt, options).toPromise();
  }

  setSelectedOrder(order) {
    this._selectedOrder = order;
  }

  getSelectedOrder() {
    return this._selectedOrder;
  }

  getAllOrders() {
    let opt = {
      paging: {
        limit: 10000
      },
      mixed: {
        all: true,
        all_shops: true,
        all_suppliers: true
      }
    };
    return this.http.post("api/shop.Order/GetOrders", opt).toPromise().catch(err => err);
  }

  cancelFulfillment(token, id) {
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Fulfillment/CancelFulfillment", { id: id }, options).toPromise();
  }

  cancelOrder(shop_id, order_id, reason) {
    const token = this.auth.getTokenByShop(shop_id);
    const options = this.getOptions(token);
    return this.http.postWithOptions("api/shop.Order/CancelOrder", { order_id: order_id, cancel_reason: reason }, options).toPromise()
      .then(res => {
        if (res.order) {
          this._pullOrder(token, res.order.id);
        }
        return res;
      });
  }

  cancelOrderWithoutToken(order_id, reason) {
    return this.http.post("api/shop.Order/CancelOrder", { order_id: order_id, cancel_reason: reason }).toPromise();
  }

  hasFulfillment(order) {
    return order.fulfillments && order.fulfillments[0];
  }

  filterShippingService(shippingServices: any[]) {
    const group = {
      'ghn': [],
      'ghtk': [],
      'vtpost': []
    };

    const providerGroup = shippingServices.reduce((providerHash, s) => {
      providerHash[s.provider].push(s);
      return providerHash;
    }, group);

    return Object.keys(providerGroup).map(key => {
      const services = providerGroup[key] as Array<any>;
      return {
        name: key,
        imageSrc: '/assets/images/ship-' + key + '.png',
        services: this.getMinPacks(services),
      };
    });
  }

  private findMinPack(services, type: ShippingType) {
    let minPack = services
      .filter(s => s.name === type)
      .reduce((min, n) => {
        if (!min) {
          return n;
        }
        return min.service_fee > n.service_fee ? n : min;
      }, undefined);

    minPack = minPack ? Object.assign(minPack, { is_available: true, _name: minPack.name, name: type }) : {
      name: type,
      is_available: false
    };
    return minPack;
  }

  getMinPacks(services) {
    let minFastPack = this.findMinPack(services, ShippingType.FAST);
    let minStandardPack = this.findMinPack(services, ShippingType.STANDARD);
    return [minFastPack, minStandardPack];
  }

  getMinPacksWihtPrority(_services: any[], _type: string, _provider: string) {
    const type = mapShippingType[_type];
    const groupServices = this.filterShippingService(_services);
    const services = [];
    groupServices.forEach(gr => {
      const provider = gr.name;
      gr.services.forEach(s => {
        if (s.is_available) {
          services.push({
            provider,
            name: s.name,
            external_id: s.external_id,
            service_fee: s.service_fee
          });
        }
      });
    });

    return services.sort((s1, s2) => {
      const fee1 = s1.service_fee;
      const fee2 = s2.service_fee;
      if (fee1 === fee2) {
        const type1 = s1.name;
        const type2 = s2.name;
        if (type1 === type2) {
          return s1.provider === _provider ? -1 : 1;
        }
        return type1 === type ? -1 : 1;
      }
      return fee1 - fee2;
    });
  }

  getTokenString(order) {
    return this.auth.getStringTokenByShop(order.shop_id);
  }

  private _addValueOption(filterOptions: List<FilterOption>, option: FilterOption, limit = this.LIMIT_CUSTOM_OPTION): List<FilterOption> {
    if (!option) {
      return filterOptions;
    }
    if (filterOptions.get(option.id)) {
      filterOptions.deleteId(option.id);
    }
    filterOptions.add(option);
    if (filterOptions.length > limit) {
      filterOptions.deleteId(filterOptions.index(0).id);
    }
    return filterOptions;
  }

  sortFilterOptions(productWeights: List<FilterOption>): List<FilterOption> {
    const arr = productWeights.sort((a, b) => {
      const num1 = Number(a.value), num2 = Number(b.value);
      return num1 - num2;
    });
    return new List(arr);
  }

  addValueOption(filterOptions: List<FilterOption>, option: FilterOption) {
    filterOptions.add(option);
    return this.sortFilterOptions(filterOptions);
  }

  mergeLocalAndDefaultOptions(localOptions: List<FilterOption>, defaultOptions: List<FilterOption>) {
    const tempList = new List<FilterOption>([ ...localOptions.asArray() ]);
    tempList.merge(defaultOptions.asArray());
    return this.sortFilterOptions(tempList);
  }

  getLastOption(filterOptions: List<FilterOption>): string {
    if (filterOptions.length === 0) {
      return null;
    }
    return filterOptions.index(filterOptions.length - 1).id;
  }

  getDataLocal() {
    this.localProductWeights = new List<FilterOption>([]);
    this.localShipValues = new List<FilterOption>([]);
    let discountOption = 'percent';
    this.localDiscountValues = new List<FilterOption>([]);
    this.localDiscountPercents = new List<FilterOption>([]);
    let lastSelectedWeight = this.defaultProductWeights.index(0).id;
    let lastShipValue = this.defaultShipValues.index(0).id;
    let lastDiscountPercent = this.defaultShipValues.index(0).id;
    let lastDiscountValue = this.defaultShipValues.index(0).id;
    try {
      let dataStr = localStorage.getItem(this.__secretCode);
      if (dataStr) {
        let data = JSON.parse(misc.decodeBase64(dataStr));
        debug.log("data", data);
        this.localProductWeights = data.productWeights ? new List(data.productWeights) : this.localProductWeights;
        this.localShipValues = data.shipValues ? new List(data.shipValues) : this.localShipValues;
        discountOption = data.discountOption || discountOption;
        this.localDiscountValues = new List(data.discountValues) || this.localDiscountValues;
        this.localDiscountPercents = new List(data.discountPercents) || this.localDiscountPercents;
        lastSelectedWeight = this.getLastOption(this.localProductWeights) || lastSelectedWeight;
        lastShipValue = this.getLastOption(this.localShipValues) || lastShipValue;
        lastDiscountPercent = this.getLastOption(this.localDiscountPercents) || lastDiscountPercent;
        lastDiscountValue = this.getLastOption(this.localDiscountValues) || lastDiscountValue;
      }
    } catch (e) {
      debug.error(e);
    }
    const productWeights = this.mergeLocalAndDefaultOptions(this.localProductWeights, this.defaultProductWeights);
    const shipValues = this.mergeLocalAndDefaultOptions(this.localShipValues, this.defaultShipValues);
    const discountValues = this.mergeLocalAndDefaultOptions(this.localDiscountValues, this.defaultShipValues);
    const discountPercents = this.mergeLocalAndDefaultOptions(this.localDiscountPercents, this.defaultShipValues);
    return {
      productWeights,
      shipValues,
      discountOption,
      discountValues,
      discountPercents,
      lastSelectedWeight,
      lastShipValue,
      lastDiscountPercent,
      lastDiscountValue,
    };
  }

  clearDataLocal() {
    localStorage.setItem(this.__secretCode, misc.encodeBase64(JSON.stringify({})));
  }

  saveDataLocal(data) {
    const { lastSelectedWeight, lastDiscountValue, lastDiscountPercent, lastShipValue, discountOption } = data;
    const productWeights = this._addValueOption(this.localProductWeights, initFilterOption(lastSelectedWeight)).asArray();
    const shipValues = this._addValueOption(this.localShipValues, initFilterOption(lastShipValue)).asArray();
    const discountValues = this._addValueOption(this.localDiscountValues, initFilterOption(lastDiscountValue)).asArray();
    const discountPercents = this._addValueOption(this.localDiscountPercents, initFilterOption(lastDiscountPercent)).asArray();

    const temp = {
      discountOption,
      productWeights,
      shipValues,
      discountValues,
      discountPercents
    };
    debug.log("temp", temp);
    localStorage.setItem(this.__secretCode, misc.encodeBase64(JSON.stringify(temp)));
  }

  get defaultDimension() {
    return {
      length: 5,
      width: 5,
      height: 5
    };
  }
}



