import {AModel} from "./AModel";
import {OrderAddress} from "./address";
import {Order} from "./order";
import {Shop} from "./account";

export class Fulfillment extends AModel {
  address_from: OrderAddress;
  address_to: OrderAddress;
  order_id: string;
  shop_id: string;
  supplier_id: string;
  lines: Array<any>;

  total_items: number;
  total_weight: number;
  total_amount: number;
  total_cod_amount: number;

  x_sync_status: string;

  created_at: Date;
  updated_at: Date;
  cancelled_at: Date;
  closed_at: Date;
  cod_etop_paid_at: string;

  shipping_provider: string;
  shipping_code: string;
  shipping_fee_customer: number;
  shipping_fee_shop: number;
  shipping_state: string;
  shipping_note: string;
  status_mapped: {
    display: string,
    state: string,
  };

  expected_delivery_at: string;
  expected_pick_at: string;

  x_shipping_delivered_at: string;
  x_shipping_picked_at: string;
  x_shipping_logs: any[];
  logs: any[];

  order: Order;
  shop: Shop;
  status: string;

  shipping_fee_shop_lines: Array<ShippingFeeShopLine>;
  shipping_fee_shop_hash: any;
  expected_picking_time: Array<Date>;

  o_data: any;
  p_data: any;
}

export class ShippingFeeShopLine {
  shipping_fee_type: string;
  cost: number;
  external_service_id: number;
  external_service_name: string;
}



