import {Injectable} from '@angular/core';
import {Subject} from "rxjs/Subject";
import {DataModel} from "./models/message";

export function getWindowRef() {
  return (typeof window !== "undefined") ? window : null;
}

@Injectable()
export class IframeService {

  onParentMessage = new Subject();

  constructor() {
    this._init();
  }

  private _init() {
    let listenMessages = ["hello", "ready",
      "access-granted",
      "init-session", "token-created",
      "get-shipping-service",
      "input-value", "submit-order", "reset-form",
      "order-created"];
    let windowRef = getWindowRef();
    if (!windowRef || !windowRef.parent) {
      debug.error("Parent reference is undefined");
    }

    this._bindEvent(windowRef, "message", (e: MessageEvent) => {
      let data = e.data;
      if (typeof data == "string") {
        try {
          data = JSON.parse(data);
        } catch (error) {
          return;
        }
      }
      if (listenMessages.indexOf(data.event) > -1) {
        this.onParentMessage.next(new DataModel(data));
      }
    });

    this.onParentMessage.subscribe((data: any) => {
      if (data.event == "hello") {
        debug.log("MERCHANT RECEIVE TEST MESSAGE", "hello", data);
        this.sendMessage(new DataModel({
          requestId: data.requestId,
          event: 'hello',
          data: {
            greeting: "Hi, this is message from Merchant client"
          }
        }));
      }
    });
    this.sendMessage(new DataModel({
      event: "ready",
      data: null
    }));
  }

  private _bindEvent(element, eventName, eventHandler) {
    if (element.addEventListener) {
      element.addEventListener(eventName, eventHandler, false);
    } else if (element.attachEvent) {
      element.attachEvent('on' + eventName, eventHandler);
    }
  }

  public sendMessage(data: DataModel) {
    let windowRef = getWindowRef();
    let parentRef = windowRef && windowRef.parent;
    if (!windowRef || !windowRef.parent) {
      debug.error("Parent reference is undefined");
    }
    let _data = typeof data == "string" ? data : JSON.stringify(data);
    parentRef.postMessage(_data, "*");
  }
}



