import { Injectable } from '@angular/core';
import { ProgressBarBottomComponent } from '../shared-components/progress-bar-bottom/progress-bar-bottom.component';

@Injectable()
export class ProgressBarBottomService {

  private progressBarComp: ProgressBarBottomComponent;

  constructor() { }

  _setProgressBarComponent(progressBarComp: ProgressBarBottomComponent) {
    this.progressBarComp = progressBarComp;
  }

  startLoading() {
    if (this.progressBarComp) {
      this.progressBarComp.startLoading();
    }
  }

  finishLoading() {
    if (this.progressBarComp) {
      this.progressBarComp.done();
    }
  }
}



