import { Injectable } from '@angular/core';
import { Subject } from "rxjs/Subject";
import { environment } from "../../environments/environment";
import misc from "../../libs/misc";

@Injectable()
export class StorageService {

  __secretCode = "";

  onLocalStorageUpdated = new Subject();
  ready = false;

  constructor() {
    this.__secretCode = misc.encodeBase64('nửa con gà');
    this.load();
  }


  get(name) {
    return this[name];
  };

  set(name, value) {
    this[name] = value;
    this.store();
    this._invokeUpdate();
    return true;
  };


  _invokeUpdate(loaded = false) {
    let data = {};
    let keys = Object.getOwnPropertyNames(this);
    keys.forEach(key => {
      if (['function'].indexOf(typeof this[key]) > -1
        || this[key] instanceof Subject) {
        return;
      }
      data[key] = this[key];
    });
    this.onLocalStorageUpdated.next({ data, loaded: loaded });
    this.ready = loaded || this.ready;
  }


  store() {
    let data = {};
    let keys = Object.getOwnPropertyNames(this);
    keys.forEach(key => {
      if (['function'].indexOf(typeof this[key]) > -1
        || this[key] instanceof Subject) {
        return;
      }
      data[key] = this[key];
    });
    let storeData = environment.production ? misc.encodeBase64(JSON.stringify(data)) : JSON.stringify(data);
    localStorage.setItem(this.__secretCode, storeData);
  }

  load() {
    let data = {};
    try {
      let storeData = localStorage.getItem(this.__secretCode);
      if (data) {
        data = environment.production ? JSON.parse(misc.decodeBase64(storeData)) : JSON.parse(storeData);
        let keys = Object.getOwnPropertyNames(data);
        keys.forEach(key => {
          if (['function'].indexOf(typeof this[key]) > -1
            || this[key] instanceof Subject) {
            return;
          }
          this[key] = data[key];
        });
        this._invokeUpdate(true);
      }
    } catch (e) {
      debug.error("failed to load saved data", e);
    }
  }

  clear() {
    let keys = Object.getOwnPropertyNames(this);
    keys.forEach(key => {
      if (['function'].indexOf(typeof this[key]) > -1
        || this[key] instanceof Subject) {
        return;
      }
      this[key] = null;
    });
    localStorage.clear();
  }

}



