import {Injectable} from '@angular/core';
import {ActionOpts, MenuOpts, NewheaderComponent, TabOps, ActionWithoutToolbar} from "../pages/newheader/newheader.component";
import { Subject } from 'rxjs/Subject';

@Injectable()
export class HeaderService {
  private _header: NewheaderComponent;
  onFromAddressChanged = new Subject();
  flagOrder;

  constructor() {
  }

  setflagOrder(flag: boolean) {
    return this.flagOrder = flag;
  }

  setHeader(header: NewheaderComponent) {
    this._header = header;
  }

  getHeader() {
    return this._header;
  }

  setActions(actions: ActionOpts[], option?) {
    if (this._header) {
      this._header.setAction(actions, option || {});
    }
  }

  setActionsWithoutToolbar(actions: ActionWithoutToolbar[]) {
    if (this._header) {
      this._header.setActionWithoutToolbar(actions);
    }
  }

  clearActionsWithoutToolbar() {
    if (this._header) {
      this._header.clearActionWithoutToolbar();
    }
  }

  clearAction() {
    if (this._header) {
      this._header.clearAction();
    }
  }

  setMenus(menus: MenuOpts[]) {
    if (this._header) {
      this._header.setMenus(menus);
    }
  }

  clearMenus() {
    if (this._header) {
      this._header.clearMenus();
    }
  }

  setTabs(tabs: TabOps[]) {
    this._header && this._header.setTabs(tabs);
  }

  clearTabs() {
    this._header && this._header.clearTabs();
  }

  startLoading() {
    this._header && this._header.setloading(true);
  }

  endLoading() {
    this._header && this._header.setloading(false);
  }

  showSelectAddress() {
    this._header && this._header.showSelectAddress();
  }

  hideSelecetAddress() {
    this._header && this._header.hideSelectAddress();
  }
}



