import { Injectable } from '@angular/core';
import { OnBoardingComponent } from '../shared-components/on-boarding/on-boarding.component';

@Injectable()
export class OnBoardingService {
  private onBoardingComp: OnBoardingComponent;
  display = true;
  constructor() { }

  setOnBoardingComponent(onBoardingComp: OnBoardingComponent) {
    this.onBoardingComp = onBoardingComp;
  }

  hide() {
    this.display = false;
    if (this.onBoardingComp) {
      this.onBoardingComp.close();
    }
  }

  restoreDisplay() {
    this.display = true;
  }

}



